import {Component, DoCheck, OnInit} from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';
import {UserService} from '../../../services/user.service';
import {FabricarService} from '../../../services/fabricar.service';
import {Fabricar} from "../../../models/fabricar";
import {SelectItem, MessageService} from "primeng/api";
import {UploadService} from '../../../services/upload.service';
import {GLOBAL} from "../../../services/global";
import {dateFormatPipe} from '../../../pipes/datePipe';
import {PedidoService} from "../../../services/pedido.service";
import {PedidoDetalleService} from "../../../services/pedidoDetalle.service";
import {PersonalService} from "../../../services/personal.service";
import {MateriaprimaService} from "../../../services/materiaprima.service";
import {Materiaprima} from "../../../models/materiaprima";
import {Message} from 'primeng/components/common/api';

//import {MessageService} from 'primeng/components/common/messageservice';


@Component({
  selector: 'fabricacion-edit',
  templateUrl: './fabricar.component.html',
  providers: [
    FabricarService,
    UserService,
    UploadService,
    PedidoService,
    PersonalService,
    MateriaprimaService,
    PedidoDetalleService,
    MessageService


  ]
})
export class FabricarComponent {
  title = 'GESTION FABRICAR';

  public identity;
  public token;
  public url: string;


  public actual: Fabricar;
  public selected: Fabricar;
  public listado: any;
  public status: string;
  displayDialog: boolean;
  newObj: boolean;
  any: any;
  auxId: any;
  es: any; //variable para localizacion del calendar

  public estados: EstadoObj;
  public selectedEstado: EstadoObj;
  public listadoEstado: any = [];
  displayDialogEstado: boolean;
  newObjEstado: boolean;


  public material: MaterialObj;
  public selectedMaterial: MaterialObj;
  public listadoMaterial: any = [];
  public listadoMaterialBorrado: any = [];
  displayDialogMaterial: boolean;
  newObjMaterial: boolean;

  public estados_escoger: SelectItem[];
  public estados_materia: SelectItem[];

  msgs: Message[] = [];

  constructor(private _route: ActivatedRoute,
              private _router: Router,
              private _usuarioService: UserService,
              private _fabricarService: FabricarService,
              private _uploadService: UploadService,
              private _pedidoService: PedidoService,
              private _personalService: PersonalService,
              private _materiaprimaService: MateriaprimaService,
              private _pedidoDetalleService: PedidoDetalleService,
              private messageService: MessageService

  ) {
    this.actual = new Fabricar('', '', 'Proceso', [], [],0,'');
    this.listado = [];
    this.getAll();
    this.identity = this._usuarioService.getIdentity();
    this.token = this._usuarioService.getToken();
    this.url = GLOBAL.url;
    //this.actual = this.identity;
    this.estados_escoger = [
      {label: "Proceso", value: "Proceso"},
      {label: "Finalizado", value: "Finalizado"},
      {label: "Paralizado", value: "Paralizado"}
    ];
    this.estados_materia = [
      {label: "Suficiente", value: "Suficiente"},
      {label: "Insuficiente", value: "Insuficiente"}
    ];
  }

  ngOnInit() {
    this.es = {
      firstDayOfWeek: 1,
      dayNames: ["domingo", "lunes", "martes", "miércoles", "jueves", "viernes", "sábado"],
      dayNamesShort: ["dom", "lun", "mar", "mié", "jue", "vie", "sáb"],
      dayNamesMin: ["D", "L", "M", "X", "J", "V", "S"],
      monthNames: ["enero", "febrero", "marzo", "abril", "mayo", "junio", "julio", "agosto", "septiembre", "octubre", "noviembre", "diciembre"],
      monthNamesShort: ["ene", "feb", "mar", "abr", "may", "jun", "jul", "ago", "sep", "oct", "nov", "dic"],
      today: 'Hoy',
      clear: 'Borrar',
      dateFormat: 'mm/dd/yy'
    }
  }

  getAll() {
    this._fabricarService.getAll().subscribe(response => {
        this.listado = response.fabricacions;
        console.log(this.listado);
      },
      error => {
        var errorMessage = <any>error;
        if (errorMessage != null) {
          this.status = 'error';
        }
      }
    );
  }

  showDialogToAdd() {
    this.newObj = true;
    this.actual = new Fabricar('', '', '', [], [],0,'');
    this.displayDialog = true;
    this.pedidoDetalle = null;
    this.responsable = this.identity;
    this.listadoEstado = [];
    this.listadoMaterial = [];
    this.listadoMaterialBorrado = [];

  }

  deleteUpdate() {
    //funcion update
    this.actual.borrado = 1;
    this.actual.pedido = this.pedidoDetalle._id;
    this.actual.responsable = this.responsable._id;
    this.actual.estado = this.listadoEstado;
    this.actual.materiales = this.listadoMaterial;

    this._fabricarService.update(this.token, this.actual).subscribe(
      response => {
        if (response.fabricacion) {

          for(let i=0; i< this.listadoMaterial.length; i++){
            this.updateStockMateriaPrima(this.listadoMaterialBorrado[i].materiaPrima, this.listadoMaterialBorrado[i].cantidad, 1);
          }
          for(let i=0; i< this.listadoMaterialBorrado.length; i++){
            this.updateStockMateriaPrima(this.listadoMaterialBorrado[i].materiaPrima, this.listadoMaterialBorrado[i].cantidad, 1);
          }
          this.status = "success";
          this.actual = new Fabricar('', '', '', [], [],0,'');
          this.getAll();
        } else {
          console.log("OBJ _id: error" + this.actual);
          this.status = "error";
        }
      },
      error => {
        console.log(<any>error);
      }
    );
    this.actual = new Fabricar('', '', '', [], [],0,'');
    this.displayDialog = false;
  }

  save() {
    let listado = [...this.listado];
    if (this.newObj) {
      // funcion save
      this.actual.estadoGeneral = 'Proceso';
      this.actual.pedido = this.pedidoDetalle._id;
      this.actual.responsable = this.responsable._id;
      this.actual.estado = this.listadoEstado;
      this.actual.materiales = this.listadoMaterial;

      this._fabricarService.save(this.token, this.actual).subscribe(
        response => {
          if (response) {

            for(let i=0; i< this.listadoMaterial.length; i++){
              this.updateStockMateriaPrima(this.listadoMaterial[i].materiaPrima, this.listadoMaterial[i].cantidad, 0);
            }


            this.status = "success";
            this.actual = new Fabricar('', '', '', [], [],0,'');
            this.getAll();

            //listado.push(this.actual);
          } else {
            this.status = "error";
          }
        },
        error => {
          console.log(<any>error);
        }
      );


    } else {

      //funcion update
      this.actual.pedido = this.pedidoDetalle._id;
      this.actual.responsable = this.responsable._id;
      this.actual.estado = this.listadoEstado;

      let listadoMaterialAntiguo = this.actual.materiales;
      this.actual.materiales = this.listadoMaterial;

      this._fabricarService.update(this.token, this.actual).subscribe(
        response => {
          if (response.fabricacion) {
            for(let i=0; i< this.listadoMaterial.length; i++){

              let checkObj = this.checkCambiosStockMaterial(listadoMaterialAntiguo, this.listadoMaterial[i]);
              console.log('CHEQUEO '+i+": " + JSON.stringify(checkObj));
              //0: material nuevo-- 1: material sin variacion cant-- 2: material con variacion cant
              if(checkObj.estado == 0){
                this.updateStockMateriaPrima(this.listadoMaterial[i].materiaPrima, this.listadoMaterial[i].cantidad, 0);
              }else if(checkObj.estado == 2){
                this.updateStockMateriaPrima(this.listadoMaterial[i].materiaPrima, checkObj.dif, 0);
              }




            }
            for(let i=0; i< this.listadoMaterialBorrado.length; i++){
              this.updateStockMateriaPrima(this.listadoMaterialBorrado[i].materiaPrima, this.listadoMaterialBorrado[i].cantidad, 1);
            }

            this.status = "success";
            this.actual = new Fabricar('', '', '', [], [],0,'');
            this.getAll();
          } else {
            console.log("OBJ _id: error" + this.actual);
            this.status = "error";
          }
        },
        error => {
          console.log(<any>error);
        }
      );
    }

    this.actual = new Fabricar('', '', '', [], [],0,'');
    this.displayDialog = false;


    //this.listado = listado;

  }

  delete() {
    let index = this.findSelectedIndex();
    this.listado = this.listado.filter((val, i) => i != index);
    this.actual = null;
    this.displayDialog = false;
  }

  close() {
    this.actual = new Fabricar('', '', '', [], [],0,'');
    this.displayDialog = false;
  }



  checkCambiosStockMaterial(listM : any, objM: any){
    let res:any = {estado: "", dif: 0};
    let estado: any = 0; //0 material nuevo -- 1 material sin variacion cantidad -- 2 material con variacion cantidad
    let dif : any = 0;
    for(let i=0; i< listM.length; i++){
      if(listM[i].materiaPrima == objM.materiaPrima) {
        if (listM[i].cantidad == objM.cantidad) {
          estado = 1;
        } else {
          estado = 2;
          dif = objM.cantidad  - listM[i].cantidad;
        }
      }
    }

    res.estado= estado;
    res.dif=dif;
    return res;

  }

  updateStockMateriaPrima(idMat: any, cant:any ,op: any){
    //op 0: menorar stock -- op 1: aumentar stock



    this._materiaprimaService.get(idMat).subscribe(
      response => {
        if (response.materiaPrima) {
          let objM = response.materiaPrima;

          if(op==0){
            objM.stock = objM.stock - cant;
          }else{
            objM.stock = objM.stock + cant;
          }
          //updatestock ************************************************************
          this._materiaprimaService.update(this.token, objM).subscribe(
            response => {
              if (response.materiaPrima) {
                this.status = "success";
              } else {
                console.log("OBJ _id: error" + this.actual);
                this.status = "error";
              }
            },
            error => {
              console.log(<any>error);
            }
          );

          //fin ********************************************************************



          this.status = "success";
        } else {
          console.log("OBJ _id: error" + this.actual);
          this.status = "error";
        }
      },
      error => {
        console.log(<any>error);
      }
    );
  }


  // FUNCIONES para el AUTOCOMPLETAR  PEDIDODETALLE ***************************************************************************
  objsFabricar: any[];
  filteredObjSinglePedido: any[];
  public pedidoDetalle: any;

  filterSinglePedido(event) {
    let query = event.query;
    this._pedidoDetalleService.getAll().subscribe(objs => {
      this.filteredObjSinglePedido = this.filterPedido(query, objs.pedidoDetalles);
    });
  }

  filterPedido(query, objs: any[]): any[] {
    //in a real application, make a request to a remote url with the query and return filtered results, for demo we filter at client side
    let filtered: any[] = [];
    for (let i = 0; i < objs.length; i++) {
      let obj = objs[i];
      if (obj.pedido.descripcion.toLowerCase().indexOf(query.toLowerCase()) >= 0) {
        filtered.push(obj);
      }
    }
    return filtered;
  }

  //FIN AUTOCOMPLETAR PEDIDO *******************************************************************************************

  // FUNCIONES para el AUTOCOMPLETAR  RESPONSABLE **********************************************************************
  filteredObjSingleResponsable: any[];
  public responsable: any;

  filterSingleResponsable(event) {
    let query = event.query;
    this._personalService.getAll().subscribe(objs => {
      this.filteredObjSingleResponsable = this.filterResponsable(query, objs.personals);
    });
  }

  filterResponsable(query, objs: any[]): any[] {
    //in a real application, make a request to a remote url with the query and return filtered results, for demo we filter at client side
    let filtered: any[] = [];
    for (let i = 0; i < objs.length; i++) {
      let obj = objs[i];
      if (obj.apellido.toLowerCase().indexOf(query.toLowerCase()) >= 0) {
        filtered.push(obj);
      }
    }
    return filtered;
  }

  //FIN AUTOCOMPLETAR RESPONSABLE **************************************************************************************

  // FUNCIONES para el AUTOCOMPLETAR  MATERIA PRIMA*********************************************************************
  filteredObjSingleMateriaPrima: any[];
  public materiaPrima: any;

  filterSingleMateriaPrima(event) {
    let query = event.query;
    this._materiaprimaService.getAll().subscribe(objs => {
      this.filteredObjSingleMateriaPrima = this.filterMateriaPrima(query, objs.materiaPrimas);
    });
  }

  filterMateriaPrima(query, objs: any[]): any[] {
    //in a real application, make a request to a remote url with the query and return filtered results, for demo we filter at client side
    let filtered: any[] = [];
    for (let i = 0; i < objs.length; i++) {
      let obj = objs[i];
      if (obj.descripcion.toLowerCase().indexOf(query.toLowerCase()) >= 0) {
        filtered.push(obj);
      }
    }
    return filtered;
  }

  //FIN AUTOCOMPLETAR MATERIA PRIMA ************************************************************************************


  //FUNCIONES para ESTADOS**********************************************************************************************
  showDialogToAddEstado() {
    this.newObjEstado = true;
    this.estados = new EstadoObj('', '');
    this.displayDialogEstado = true;
  }

  saveEstado() {
    let listadoEstado = [...this.listadoEstado];
    if (this.newObjEstado)
      listadoEstado.push(this.estados);
    else
      listadoEstado[this.findSelectedIndexEstado()] = this.estados;

    this.listadoEstado = listadoEstado;
    this.estados = new EstadoObj('', '');
    this.displayDialogEstado = false;
  }

  deleteEstado() {
    let index = this.findSelectedIndexEstado();
    this.listadoEstado = this.listadoEstado.filter((val, i) => i != index);
    this.estados = null;
    this.displayDialogEstado = false;
  }

  onRowSelectEstado(event) {
    this.newObjEstado = false;
    var datePipe = new dateFormatPipe();
    event.data['fecha'] = datePipe.transform(event.data['fecha']);
    this.estados = this.cloneObjEstado(event.data);
    this.displayDialogEstado = true;


  }

  cloneObjEstado(obj: any): any {
    let actual = new EstadoObj('', '');
    for (let prop in obj) {
      actual[prop] = obj[prop];
    }
    return actual;
  }

  findSelectedIndexEstado(): number {
    return this.listadoEstado.indexOf(this.selectedEstado);
  }

  //FIN FUNCIONES ESTADOS***********************************************************************************************


  //FUNCIONES para MATERIALES*******************************************************************************************

  showDialogToAddMaterial() {
    this.newObjMaterial = true;
    this.material = new MaterialObj('', '', '', '');
    this.displayDialogMaterial = true;
    this.materiaPrima = null;


  }

  controlStock(){
    if(this.materiaPrima.stock >= this.material.cantidad){
      return true;
    }
    return false
  }

  saveMaterial() {

    if(this.controlStock()){
      let listadoMaterial = [...this.listadoMaterial];
      if (this.newObjMaterial) {
        this.material.materiaPrima = this.materiaPrima;
        this.material.estado = 'Suficiente';
        listadoMaterial.push(this.material);
      }
      else {
        this.material.materiaPrima = this.materiaPrima;
        listadoMaterial[this.findSelectedIndexMaterial()] = this.material;
      }
      this.listadoMaterial = listadoMaterial;
      this.material = new MaterialObj('', '', '', '');
      this.displayDialogMaterial = false;
    }else{
      this.msgs = [];
      this.msgs.push({severity:'warn', summary:'Advertencia', detail:'STOCK insuficiente'});
    }

  }

  deleteMaterial() {
    let index = this.findSelectedIndexMaterial();
    this.listadoMaterialBorrado = this.selectedMaterial;
    this.listadoMaterial = this.listadoMaterial.filter((val, i) => i != index);
    this.material = null;
    this.displayDialogMaterial = false;
    console.log("matariales:" +JSON.stringify(this.listadoMaterial));
    console.log("matarialesBORRADOS:" +JSON.stringify(this.listadoMaterialBorrado));

  }

  onRowSelectMaterial(event) {
    this.newObjMaterial = false;
    this.material = this.cloneObjMaterial(event.data);
    this.displayDialogMaterial = true;
    this.materiaPrima = this.material.materiaPrima;

    console.log("maPrAux " + JSON.stringify(this.materiaPrima));
  }

  cloneObjMaterial(obj: any): any {
    let actual = new MaterialObj('', '', '', '');
    for (let prop in obj) {
      actual[prop] = obj[prop];
    }
    return actual;
  }

  findSelectedIndexMaterial(): number {
    return this.listadoMaterial.indexOf(this.selectedMaterial);
  }


  //FIN FUNCIONES MATERIALES********************************************************************************************


  onRowSelect(event) {
    this.newObj = false;
    this.actual = this.cloneObj(event.data);
    this.displayDialog = true;
    this.pedidoDetalle = this.actual.pedido;
    this.responsable = this.actual.responsable;
    this.listadoEstado = this.actual.estado;
    this.listadoMaterial = this.actual.materiales;
    this.listadoMaterialBorrado = [];
  }

  cloneObj(obj: Fabricar): Fabricar {
    let actual = new PrimeObj('', '', '', [], [],0,'');
    for (let prop in obj) {
      actual[prop] = obj[prop];
    }
    return actual;
  }

  findSelectedIndex(): number {
    return this.listado.indexOf(this.selected);
  }


  public filesToUpload: Array<File>;

  fileChangeEvent(fileInput: any) {
    this.filesToUpload = <Array<File>>fileInput.target.files;

  }

}

class PrimeObj implements Fabricar {

  constructor(public pedido, public responsable, public estadoGeneral, public estado, public materiales, public borrado, public _id) {
  }
}


class EstadoObj {
  constructor(public descripcion, public fecha) {

  }
}

class MaterialObj {
  constructor(public materiaPrima, public cantidad, public uMedida, public estado) {

  }
}





