import {Component, DoCheck, OnInit} from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';
import {UserService} from '../../../services/user.service';
import {CategoriaService} from '../../../services/categoria.service';
import {Categoria} from "../../../models/categoria";
import {SelectItem} from "primeng/api";
import {UploadService} from '../../../services/upload.service';
import {GLOBAL} from "../../../services/global";


@Component({
  selector: 'vendedor-edit',
  templateUrl: './categoria.component.html',
  providers: [
    CategoriaService,
    UserService,
    UploadService
  ]
})
export class CategoriaComponent {
  title = 'GESTION CATEGORIAS';

  public identity;
  public token;
  public url: string;


  public actual: Categoria;
  public selected: Categoria;
  public listado: any;
  public status: string;
  displayDialog: boolean;
  newObj: boolean;
  any: any;
  auxId: any;

  public roles: SelectItem[];


  constructor(private _route: ActivatedRoute,
              private _router: Router,
              private _usuarioService: UserService,
              private _categoriaService: CategoriaService,
              private _uploadService: UploadService
  ) {
    this.actual = new Categoria('', '',0);
    this.listado = [];
    this.getAll();
    this.identity = this._usuarioService.getIdentity();
    this.token = this._usuarioService.getToken();
    this.url = GLOBAL.url;
    //this.actual = this.identity;

  }

  ngOnInit() {

  }

  getAll() {
    this._categoriaService.getAll().subscribe(response => {
        this.listado = response.categorias;
        console.log(this.listado);
      },
      error => {
        var errorMessage = <any>error;
        if (errorMessage != null) {
          this.status = 'error';
        }
      }
    );
  }

  showDialogToAdd() {
    this.newObj = true;
    this.actual = new Categoria('', '',0);
    this.displayDialog = true;

  }

  deleteUpdate() {
    //funcion update
    this.actual.borrado = 1;
    this._categoriaService.update(this.token, this.actual).subscribe(
      response => {
        if (response.categoria) {
          this.status = "success";
          this.actual = new Categoria('', '',0);
          this.getAll();

        } else {
          console.log("OBJ _id: error" + JSON.stringify(response));
          this.status = "error";
        }
      },
      error => {
        console.log(<any>error);
      }
    );
    this.actual = new Categoria('', '',0);
    this.displayDialog = false;
  }

  save() {
    let listado = [...this.listado];
    if (this.newObj) {
      // funcion save


      this._categoriaService.save(this.token, this.actual).subscribe(
        response => {
          if (response) {

              this.status = "success";
              this.actual = new Categoria('', '',0);
              this.getAll();

            //listado.push(this.actual);
          } else {
            this.status = "error";
          }
        },
        error => {
          console.log(<any>error);
        }
      );


    } else {

      //funcion update
      this._categoriaService.update(this.token, this.actual).subscribe(
        response => {
          if (response.categoria) {
              this.status = "success";
              this.actual = new Categoria('', '',0);
              this.getAll();

          } else {
            console.log("OBJ _id: error" + JSON.stringify(response));
            this.status = "error";
          }
        },
        error => {
          console.log(<any>error);
        }
      );
    }

    this.actual = new Categoria('', '',0);
    this.displayDialog = false;


    //this.listado = listado;

  }

  delete() {
    let index = this.findSelectedIndex();
    this.listado = this.listado.filter((val, i) => i != index);
    this.actual = null;
    this.displayDialog = false;
  }

  close() {
    this.actual = new Categoria('', '',0);
    this.displayDialog = false;
  }

  onRowSelect(event) {
    this.newObj = false;
    this.actual = this.cloneObj(event.data);
    this.displayDialog = true;
  }

  cloneObj(obj: Categoria): Categoria {
    let actual = new PrimeObj('', '',0);
    for (let prop in obj) {
      actual[prop] = obj[prop];
    }
    return actual;
  }

  findSelectedIndex(): number {
    return this.listado.indexOf(this.selected);
  }

}


class PrimeObj implements Categoria {

  constructor(public nombre, public descripcion, public borrado) {
  }
}



