//Imports modulos
import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {FormsModule} from '@angular/forms';
import {HttpModule} from '@angular/http';
import {FabricacionRoutingModule} from './fabricacion-routing.module';

//modulos ngPRIME
import {DataTableModule,SharedModule} from 'primeng/primeng';
import {ButtonModule} from 'primeng/primeng';
import {DialogModule} from 'primeng/primeng';
import {InputTextModule} from 'primeng/primeng';
import {PanelModule} from 'primeng/primeng';
import {DataListModule} from 'primeng/primeng';
import {CalendarModule} from 'primeng/primeng';
import {InputSwitchModule} from 'primeng/primeng';
import {GrowlModule,Message} from 'primeng/primeng';
import {TabViewModule} from 'primeng/primeng';
import {PasswordModule} from 'primeng/password';
import {DropdownModule} from 'primeng/dropdown';
import { AutoCompleteModule } from 'primeng/primeng';
import {KeyFilterModule} from 'primeng/keyfilter';
import {  MessagesModule} from 'primeng/primeng';



//prime icons fa fa-
import { AngularFontAwesomeModule } from 'angular-font-awesome';

//components
import {MainComponent} from './components/main/main.component';
import {FabricarComponent} from './components/fabricar/fabricar.component';



//Guards
import {UserService} from "../services/user.service";
import {FabricadorGuard} from "../services/fabricador.guard";
import {User} from "../models/user";


//Decorar ngModule para cargar los componentes y la configuracion de los modulos
@NgModule({
  imports:[
    CommonModule,
    FormsModule,
    HttpModule,
    FabricacionRoutingModule,

    AngularFontAwesomeModule,


    DropdownModule,
    PasswordModule,
    DataTableModule,
    SharedModule,
    ButtonModule,
    DialogModule,
    InputTextModule,
    PanelModule,
    DataListModule,
    CalendarModule,
    InputSwitchModule,
    GrowlModule,
    TabViewModule,
    AutoCompleteModule,
    KeyFilterModule,
    MessagesModule



  ],
  declarations:[
    MainComponent,
    FabricarComponent

  ],

  exports:[
    MainComponent,
    FabricarComponent
  ],
  providers:[
    UserService,
    FabricadorGuard
  ]
})

export class FabricacionModule {}



