import {Component, DoCheck, OnInit} from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';
import {UserService} from '../../../services/user.service';
import {PedidoService} from '../../../services/pedido.service';
import {PedidoDetalleService} from '../../../services/pedidoDetalle.service';
import {Pedido} from "../../../models/pedido";
import {PedidoDetalle} from "../../../models/pedidoDetalle";

import {SelectItem} from "primeng/api";
import {UploadService} from '../../../services/upload.service';
import {GLOBAL} from "../../../services/global";
import {dateFormatPipe} from '../../../pipes/datePipe';
import {ClienteService} from '../../../services/cliente.service';
import {ModeloService} from "../../../services/modelo.service";
import {PersonalService} from "../../../services/personal.service";
import {IvaService} from "../../../services/iva.service";


@Component({
  selector: 'vendedor-edit',
  templateUrl: './pedido.component.html',
  providers: [
    PedidoService,
    PedidoDetalleService,
    UserService,
    UploadService,
    ClienteService,
    ModeloService,
    PersonalService,
    IvaService
  ]
})
export class PedidoComponent {
  title = 'GESTION PEDIDOS';

  public identity;
  public token;
  public url: string;


  public actualDetalle: PedidoDetalle;
  public selectedDetalle: PedidoDetalle;
  public listadoDetalle: any;
  public listadoDetalleEliminados: any;
  displayDialogDetalle: boolean;
  newObjDetalle: boolean;

  public actual: Pedido;
  public selected: Pedido;
  public listado: any;
  public status: string;
  displayDialog: boolean;
  newObj: boolean;
  any: any;
  auxId: any;

  public listadoIva: any;

  es: any; //variable para localizacion del calendar

  public estados: SelectItem[];


  constructor(private _route: ActivatedRoute,
              private _router: Router,
              private _usuarioService: UserService,
              private _pedidoService: PedidoService,
              private _pedidoDetalleService: PedidoDetalleService,
              private _uploadService: UploadService,
              private _clienteService: ClienteService,
              private _modeloService: ModeloService,
              private _personalService: PersonalService,
              private _ivaService: IvaService,
  ) {
    this.actual = new Pedido('', '', 0, '', new Date(), new Date(), '', 0);
    this.actualDetalle = new PedidoDetalle(0, 0, 0, 0, 0, 0, 0, '', '', '', '', 0);
    this.listado = [];
    this.listadoDetalle = [];
    this.listadoDetalleEliminados = [];
    this.listadoIva = [];
    this.getAll();
    this.getIvaPorcentaje();
    this.identity = this._usuarioService.getIdentity();
    this.token = this._usuarioService.getToken();


    this.url = GLOBAL.url;
    this.estados = [
      {label: "Proceso", value: "Proceso"},
      {label: "Finalizado", value: "Finalizado"},
      {label: "Paralizado", value: "Paralizado"}
    ];
  }

  ngOnInit() {
    this.es = {
      firstDayOfWeek: 1,
      dayNames: ["domingo", "lunes", "martes", "miércoles", "jueves", "viernes", "sábado"],
      dayNamesShort: ["dom", "lun", "mar", "mié", "jue", "vie", "sáb"],
      dayNamesMin: ["D", "L", "M", "X", "J", "V", "S"],
      monthNames: ["enero", "febrero", "marzo", "abril", "mayo", "junio", "julio", "agosto", "septiembre", "octubre", "noviembre", "diciembre"],
      monthNamesShort: ["ene", "feb", "mar", "abr", "may", "jun", "jul", "ago", "sep", "oct", "nov", "dic"],
      today: 'Hoy',
      clear: 'Borrar',
      dateFormat: 'mm/dd/yy'
    }
  }

  getAll() {
    this._pedidoService.getAll().subscribe(response => {
        this.listado = response.pedidos;

        console.log(this.listado);
      },
      error => {
        var errorMessage = <any>error;
        if (errorMessage != null) {
          this.status = 'error';
        }
      }
    );
  }

  getIvaPorcentaje() {
    this._ivaService.getAll().subscribe(response => {
        this.listadoIva = response.ivas;
        this.ivaPorcentaje = this.listadoIva[0].valor;
      },
      error => {
        var errorMessage = <any>error;
        if (errorMessage != null) {
          this.status = 'error';
        }
      }
    );
  }

  showDialogToAdd() {
    this.newObj = true;
    this.actual = new Pedido('', '', 0, '', new Date(), new Date(), '', 0);
    this.displayDialog = true;
    this.cliente = null;
    this.modelo = null;
    this.personal = this.identity;
    this.listadoDetalle=[];
    this.listadoDetalleEliminados=[];
  }

  deleteUpdate() {
    //funcion delete
    this.actual.borrado = 1;
    //this.actualDetalle.modelo = this.modelo._id;
    this.actual.responsable = this.personal._id;
    this.actual.cliente = this.cliente;

    this._pedidoService.update(this.token, this.actual).subscribe(
      response => {
        if (response.pedido) {
          this.status = "success";
          this.actual = new Pedido('', '', 0, '', new Date(), new Date(), '', 0);
          this.getAll();

        } else {
          console.log("OBJ _id: error" + JSON.stringify(response));
          this.status = "error";
        }
      },
      error => {
        console.log(<any>error);
      }
    );
    this.actual = new Pedido('', '', 0, '', new Date(), new Date(), '', 0);
    this.displayDialog = false;
  }

  save() {
    let listado = [...this.listado];

    if (this.newObj) {
      // funcion save
      this.actual.responsable = this.personal._id;
      this.actual.cliente = this.cliente;
      this.actual.estado = 'Proceso';

      console.log("pedido: " + JSON.stringify(this.actual));

      this._pedidoService.save(this.token, this.actual).subscribe(
        response => {
          if (response) {

            for (let i = 0; i < this.listadoDetalle.length; i++) {
              let obj: any;
              obj = this.listadoDetalle[i];
              obj.pedido = response.pedido._id;
              this.saveDetalleDB(obj);
            }
            this.listadoDetalle = [];


            this.status = "success";
            this.actual = new Pedido('', '', 0, '', new Date(), new Date(), '', 0);
            this.getAll();

            //listado.push(this.actual);
          } else {
            this.status = "error";
          }
        },
        error => {
          console.log(<any>error);
        }
      );


    } else {

      //funcion update
      this.actual.responsable = this.personal._id;
      this.actual.cliente = this.cliente;
      this.actual.estado = 'Proceso';

      this._pedidoService.update(this.token, this.actual).subscribe(
        response => {
          if (response.pedido) {

            for (let i = 0; i < this.listadoDetalle.length; i++) {
              let obj: any;
              obj = this.listadoDetalle[i];

              if (obj.pedido == '' || obj.pedido == null) {
                obj.pedido = response.pedido._id;
                this.saveDetalleDB(obj);
              } else {
                this.updateDetalleDB(obj);
              }
            }

            for (let i = 0; i < this.listadoDetalleEliminados.length; i++) {
              let obj: any;
              obj = this.listadoDetalle[i];
              this.deleteDetalleDb(obj);
            }
            this.listadoDetalle = [];
            this.listadoDetalleEliminados = [];

            this.status = "success";
            this.actual = new Pedido('', '', 0, '', new Date(), new Date(), '', 0);
            this.getAll();

          } else {
            console.log("OBJ _id: error" + JSON.stringify(response));
            this.status = "error";
          }
        },
        error => {
          console.log(<any>error);
        }
      );
    }

    this.actual = new Pedido('', '', 0, '', new Date(), new Date(), '', 0);
    this.displayDialog = false;


    //this.listado = listado;

  }

  saveDetalleDB(obj: any) {
    // funcion save
    let objStored : any= obj;
    objStored.estado = 'Proceso';

    this._pedidoDetalleService.save(this.token, objStored).subscribe(
      response => {
        if (response) {
          this.status = "success";
        } else {
          this.status = "error";
        }
      },
      error => {
        console.log(<any>error);
      }
    );
  }

  updateDetalleDB(obj: any) {
    // funcion update
    let objStored : any= obj;
    objStored.estado = 'Proceso';
    this._pedidoDetalleService.update(this.token, objStored).subscribe(
      response => {
        if (response) {
          this.status = "success";
        } else {
          this.status = "error";
        }
      },
      error => {
        console.log(<any>error);
      }
    );
  }

  deleteDetalleDb(obj: any) {
    // funcion delete
    let objStored : any= obj;
    this._pedidoDetalleService.delete(this.token, objStored).subscribe(
      response => {
        if (response) {
          this.status = "success";
        } else {
          this.status = "error";
        }
      },
      error => {
        console.log(<any>error);
      }
    );
  }


  // FUNCIONES para el AUTOCOMPLETAR  CLIENTE **************************************************************************
  objsCliente: any[];
  filteredObjSingleCliente: any[];
  public cliente: any;

  filterSingleCliente(event) {
    let query = event.query;

    this._clienteService.getAll().subscribe(objs => {
      //var auxObjs = objs;

      this.filteredObjSingleCliente = this.filterCliente(query, objs.clientes);
    });
  }

  filterCliente(query, objs: any[]): any[] {
    //in a real application, make a request to a remote url with the query and return filtered results, for demo we filter at client side
    let filtered: any[] = [];
    for (let i = 0; i < objs.length; i++) {
      let cliente = objs[i];
      if (cliente.apellido.toLowerCase().indexOf(query.toLowerCase()) >= 0) {
        filtered.push(cliente);
      }
    }
    return filtered;
  }

  //FIN AUTOCOMPLETAR CLIENTE ******************************************************************************************

  // FUNCIONES para el AUTOCOMPLETAR  MODELO **************************************************************************
  objsModelo: any[];
  filteredObjSingleModelo: any[];
  public modelo: any;
  public precioModelo: any;

  filterSingleModelo(event) {
    let query = event.query;
    this._modeloService.getAll().subscribe(objs => {
      this.filteredObjSingleModelo = this.filterModelo(query, objs.modelos);
    });
  }

  filterModelo(query, objs: any[]): any[] {
    //in a real application, make a request to a remote url with the query and return filtered results, for demo we filter at client side
    let filtered: any[] = [];
    for (let i = 0; i < objs.length; i++) {
      let obj = objs[i];
      if (obj.nombre.toLowerCase().indexOf(query.toLowerCase()) >= 0) {
        filtered.push(obj);
      }
    }
    return filtered;
  }

  //FIN AUTOCOMPLETAR MODELO ******************************************************************************************

  // FUNCIONES para el AUTOCOMPLETAR  PERSONAL RESPONSABLE **************************************************************************
  objsPersonal: any[];
  filteredObjSinglePersonal: any[];
  public personal: any;

  filterSinglePersonal(event) {
    let query = event.query;
    this._usuarioService.getAll().subscribe(objs => {
      this.filteredObjSinglePersonal = this.filterPersonal(query, objs.users);
    });
  }

  filterPersonal(query, objs: any[]): any[] {
    //in a real application, make a request to a remote url with the query and return filtered results, for demo we filter at client side
    let filtered: any[] = [];
    for (let i = 0; i < objs.length; i++) {
      let obj = objs[i];
      if (obj.surname.toLowerCase().indexOf(query.toLowerCase()) >= 0) {
        filtered.push(obj);
      }
    }
    return filtered;
  }

  //FIN AUTOCOMPLETAR PERSONAL RESPONSABLE ******************************************************************************************


  // INICIO funciones pedido cab ***************************************************************************************
  delete() {
    let index = this.findSelectedIndex();
    this.listado = this.listado.filter((val, i) => i != index);
    this.actual = null;
    this.displayDialog = false;
  }

  close() {
    this.actual = new Pedido('', '', 0, '', new Date(), new Date(), '', 0);
    this.displayDialog = false;
    this.getAll();
  }

  getAllByPedido(id: any){
    this._pedidoDetalleService.getAllByPedido(id).subscribe(response => {
        this.listadoDetalle = response.pedidoDetalles;

        console.log(this.listadoDetalle);

      },
      error => {
        var errorMessage = <any>error;
        if (errorMessage != null) {
          this.status = 'error';
        }
      }
    );
  }

  onRowSelect(event) {
    var datePipe = new dateFormatPipe();
    event.data['fecha'] = datePipe.transform(event.data['fecha']);
    event.data['fecha_fin'] = datePipe.transform(event.data['fecha_fin']);
    console.log("PEDIDO: "+ JSON.stringify(event.data['_id']));
    this.newObj = false;
    this.actual = this.cloneObj(event.data);
    this.displayDialog = true;
    this.cliente = this.actual.cliente;
    //this.modelo = this.actualDetalle.modelo;
    //this.precioModelo = this.modelo.precio;
    this.personal = this.actual.responsable;
    this.getAllByPedido(event.data['_id']);
  }

  cloneObj(obj: Pedido): Pedido {
    let actual = new PrimeObj('', '', '', 0, new Date(), new Date(), '', 0);
    for (let prop in obj) {
      actual[prop] = obj[prop];
    }
    return actual;
  }

  findSelectedIndex(): number {
    return this.listado.indexOf(this.selected);
  }

  //FIN funciones detalle cab ******************************************************************************************


  // INICIO funciones pedido DETALLE ***************************************************************************************

  showDialogToAddDetalle() {
    this.newObjDetalle = true;
    this.actualDetalle = new PedidoDetalle(0, 0, 0, 0, 0, 0, 0, '', '', '', '', 0);
    this.displayDialogDetalle = true;
    this.modelo = null;
  }

  saveDetalle() {
    let detalles = [...this.listadoDetalle];
    if (this.controlSaveDetalle()) {
      this.actualDetalle.modelo = this.modelo;
      if (this.newObjDetalle) {
        detalles.push(this.actualDetalle);
      }else{
        detalles[this.listadoDetalle.indexOf(this.selectedDetalle)] = this.actualDetalle;
      }
    }
    this.precioModelo = 0;
    this.actualDetalle = new PedidoDetalle(0, 0, 0, 0, 0, 0, 0, '', '', '', '', 0);
    this.modelo = null;
    this.listadoDetalle = detalles;
    this.displayDialogDetalle = false;
    this.calcularTotalGeneral();
  }

  controlSaveDetalle() {

    if (this.actualDetalle.ancho == 0 ||
      this.actualDetalle.alto == 0 ||
      this.actualDetalle.cantidad == 0) {
      return false;
    }

    return true;
  }


  deleteDetalle() {
    this.listadoDetalleEliminados.push(this.selectedDetalle);
    let index = this.findSelectedIndexDetalle();
    this.listadoDetalle = this.listadoDetalle.filter((val, i) => i != index);
    this.actualDetalle = null;
    this.displayDialogDetalle = false;
    this.calcularTotalGeneral();
  }

  closeDetalle() {
    this.actualDetalle = new PedidoDetalle(0, 0, 0, 0, 0, 0, 0, '', '', '', '', 0);
    this.displayDialogDetalle = false;
    this.getAll();
  }

  onRowSelectDetalle(event) {
    this.newObjDetalle = false;
    this.actualDetalle = this.cloneObjDetalle(event.data);
    this.displayDialogDetalle = true;
    this.modelo = this.actualDetalle.modelo;
    this.precioModelo = this.modelo.precio;
  }

  cloneObjDetalle(obj: PedidoDetalle): PedidoDetalle {
    let actual = new PrimeObjDetalle(0, 0, 0, 0, 0, 0, 0, '', '', '', '', 0);
    for (let prop in obj) {
      actual[prop] = obj[prop];
    }
    return actual;
  }

  findSelectedIndexDetalle(): number {
    return this.listadoDetalle.indexOf(this.selectedDetalle);
  }



  public ivaPorcentaje: any;

  calcularTotal(event) {
    var pvp_aux: any = (this.actualDetalle.ancho * this.actualDetalle.alto * this.actualDetalle.cantidad * this.precioModelo);
    var iva_aux: any = ((pvp_aux - this.actualDetalle.descuento) * this.ivaPorcentaje / 100);
    var total_aux: any = ((pvp_aux - this.actualDetalle.descuento) + iva_aux);

    this.actualDetalle.pvp = pvp_aux.toFixed(2);
    this.actualDetalle.iva = iva_aux.toFixed(2);
    this.actualDetalle.total = total_aux.toFixed(2);

  }

  cambiarPrecio(event) {
    //console.log("obj " + JSON.stringify(event));
    this.precioModelo = event.precio;
    this.calcularTotal(event);
  }


  calcularTotalGeneral() {
    var tot: any=0;
    var obj: any;
    for (let i = 0; i < this.listadoDetalle.length; i++) {
      obj = this.listadoDetalle[i];
      tot = Number(tot)+Number(obj.total);
    }

    this.actual.total = tot.toFixed(2);
  }

  //FIN funciones detalle DETALLE ******************************************************************************************


}


class PrimeObj implements Pedido {

  constructor(public cliente, public responsable, public total, public descripcion, public fecha, public fecha_fin, public estado, public borrado) {
  }
}

class PrimeObjDetalle implements PedidoDetalle {

  constructor(public ancho, public alto, public cantidad, public pvp, public descuento, public iva, public total, public descripcion, public estado, public pedido, public modelo, public borrado) {
  }
}



