//Imports modulos
import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {HttpModule} from '@angular/http';
import {AdminRoutingModule} from './admin-routing.module';

//modulos ngPRIME
import {DataTableModule, SharedModule} from 'primeng/primeng';
import {ButtonModule} from 'primeng/primeng';
import {DialogModule} from 'primeng/primeng';
import {InputTextModule} from 'primeng/primeng';
import {PanelModule} from 'primeng/primeng';
import {DataListModule} from 'primeng/primeng';
import {CalendarModule} from 'primeng/primeng';
import {InputSwitchModule} from 'primeng/primeng';
import {GrowlModule} from 'primeng/primeng';
import {TabViewModule} from 'primeng/primeng';
import {PasswordModule} from 'primeng/password';
import {DropdownModule} from 'primeng/dropdown';
import {ToastModule} from 'primeng/toast';
import { AutoCompleteModule } from 'primeng/primeng';
import {KeyFilterModule} from 'primeng/keyfilter';
import {TableModule} from 'primeng/table';
import { PaginatorModule } from 'primeng/primeng';
import {MultiSelectModule} from 'primeng/multiselect';
// Here


//prime icons fa fa-
import { AngularFontAwesomeModule } from 'angular-font-awesome';

//components
import {MainComponent} from './components/main/main.component';
import {PersonalComponent} from './components/personal/personal.component';
import {ListComponent} from './components/list/list.component';
import {MateriaprimaComponent} from './components/materiaprima/materiaprima.component';
import {CategoriaComponent} from './components/categoria/categoria.component';
import {IvaComponent} from './components/iva/iva.component';


//Guards
import {UserService} from "../services/user.service";
import {AdminGuard} from "../services/admin.guard";
import {User} from "../models/user";

//Decorar ngModule para cargar los componentes y la configuracion de los modulos
@NgModule({
  imports:[

    CommonModule,
    FormsModule,
    HttpModule,
    AdminRoutingModule,

    AngularFontAwesomeModule,



    DropdownModule,
    PasswordModule,
    DataTableModule,
    SharedModule,
    ButtonModule,
    DialogModule,
    InputTextModule,
    PanelModule,
    DataListModule,
    CalendarModule,
    InputSwitchModule,
    GrowlModule,
    TabViewModule,
    KeyFilterModule,
    AutoCompleteModule,
    ToastModule,
    ReactiveFormsModule,
    TableModule,
    PaginatorModule,
    MultiSelectModule











  ],


  declarations:[
    MainComponent,
    ListComponent,
    PersonalComponent,
    MateriaprimaComponent,
    CategoriaComponent,
    IvaComponent


  ],

  exports:[
    MainComponent,
    ListComponent,
    PersonalComponent,
    MateriaprimaComponent,
    CategoriaComponent
  ],
  providers:[
    UserService,
    AdminGuard,
  ]
})

export class AdminModule {}



