import {Component, DoCheck, OnInit} from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';
import {UserService} from '../../../services/user.service';
import {ColocarService} from '../../../services/colocar.service';
import {Colocar} from "../../../models/colocar";
import {SelectItem} from "primeng/api";
import {UploadService} from '../../../services/upload.service';
import {GLOBAL} from "../../../services/global";
import {dateFormatPipe} from '../../../pipes/datePipe';
import {PedidoService} from "../../../services/pedido.service";
import {PersonalService} from "../../../services/personal.service";
import {MateriaprimaService} from "../../../services/materiaprima.service";
import {PedidoDetalleService} from "../../../services/pedidoDetalle.service";
import {FabricarService} from "../../../services/fabricar.service";



@Component({
  selector: 'colocacion-edit',
  templateUrl: './colocar.component.html',
  providers: [
    ColocarService,
    UserService,
    UploadService,
    PedidoService,
    PersonalService,
    MateriaprimaService,
    PedidoDetalleService,
    FabricarService
  ]
})
export class ColocarComponent {
  title = 'GESTION COLOCAR';

  public identity;
  public token;
  public url: string;


  public actual: Colocar;
  public selected: Colocar;
  public listado: any;
  public status: string;
  displayDialog: boolean;
  newObj: boolean;
  any: any;
  auxId: any;
  es:any; //variable para localizacion del calendar

  public estados:EstadoObj;
  public selectedEstado:EstadoObj;
  public listadoEstado:any = [];
  displayDialogEstado:boolean;
  newObjEstado:boolean;


  public estadoGeneral : SelectItem[];
  public roles: SelectItem[];


  constructor(private _route: ActivatedRoute,
              private _router: Router,
              private _usuarioService: UserService,
              private _colocarService: ColocarService,
              private _uploadService: UploadService,
              private _pedidoService: PedidoService,
              private _personalService: PersonalService,
              private _fabricarService: FabricarService
  ) {
    this.actual = new Colocar('', '', '', [],0,'');
    this.listado = [];
    this.getAll();
    this.identity = this._usuarioService.getIdentity();
    this.token = this._usuarioService.getToken();
    this.url = GLOBAL.url;
    //this.actual = this.identity;

    this.estadoGeneral = [
      {label: "Proceso", value: "Proceso"},
      {label: "Finalizado", value: "Finalizado"},
      {label: "Paralizado", value: "Paralizado"}
    ];

  }

  ngOnInit() {
    this.es = {
      firstDayOfWeek: 1,
      dayNames: ["domingo", "lunes", "martes", "miércoles", "jueves", "viernes", "sábado"],
      dayNamesShort: ["dom", "lun", "mar", "mié", "jue", "vie", "sáb"],
      dayNamesMin: ["D", "L", "M", "X", "J", "V", "S"],
      monthNames: ["enero", "febrero", "marzo", "abril", "mayo", "junio", "julio", "agosto", "septiembre", "octubre", "noviembre", "diciembre"],
      monthNamesShort: ["ene", "feb", "mar", "abr", "may", "jun", "jul", "ago", "sep", "oct", "nov", "dic"],
      today: 'Hoy',
      clear: 'Borrar',
      dateFormat: 'mm/dd/yy'
    }
  }

  getAll() {
    this._colocarService.getAll().subscribe(response => {
        this.listado = response.colocacions;
        console.log("colocacions: " + JSON.stringify(this.listado));
      },
      error => {
        var errorMessage = <any>error;
        if (errorMessage != null) {
          this.status = 'error';
        }
      }
    );
  }

  showDialogToAdd() {
    this.newObj = true;
    this.actual = new Colocar('', '', '', [],0,'');
    this.displayDialog = true;
    this.fabricacion = null;
    this.responsable = this.identity;
    this.listadoEstado = [];

  }


  deleteUpdate() {
    //funcion update
    this.actual.borrado = 1;
    this.actual.pedido = this.fabricacion._id;
    this.actual.responsable = this.responsable._id;
    this.actual.estado = this.listadoEstado;

    this._colocarService.update(this.token, this.actual).subscribe(
      response => {
        if (response.colocacion) {
          this.status = "success";
          this.actual = new Colocar('', '', '', [],0,'');
          this.getAll();
        } else {
          console.log("OBJ _id: error" + this.actual);
          this.status = "error";
        }
      },
      error => {
        console.log(<any>error);
      }
    );
    this.actual = new Colocar('', '', '', [],0,'');
    this.displayDialog = false;
  }

  save() {
    let listado = [...this.listado];
    if (this.newObj) {
      // funcion save
      if(this.actual.estadoGeneral==''){
        this.actual.estadoGeneral = "Proceso";
      }

      this.actual.pedido = this.fabricacion._id;
      this.actual.responsable = this.responsable._id;
      this.actual.estado = this.listadoEstado;

      this._colocarService.save(this.token, this.actual).subscribe(
        response => {
          if (response) {

            this.status = "success";
            this.actual = new Colocar('', '', '', [],0,'');
            this.getAll();

            //listado.push(this.actual);
          } else {
            this.status = "error";
          }
        },
        error => {
          console.log(<any>error);
        }
      );


    } else {

      //funcion update
      this.actual.pedido = this.fabricacion._id;
      this.actual.responsable = this.responsable._id;
      this.actual.estado = this.listadoEstado;

      this._colocarService.update(this.token, this.actual).subscribe(
        response => {
          if (response.colocacion) {
            this.status = "success";
            this.actual = new Colocar('', '', '', [],0,'');
            this.getAll();
          } else {
            console.log("OBJ _id: error" + this.actual);
            this.status = "error";
          }
        },
        error => {
          console.log(<any>error);
        }
      );
    }

    this.actual = new Colocar('', '', '', [],0,'');
    this.displayDialog = false;


    //this.listado = listado;

  }

  delete() {
    let index = this.findSelectedIndex();
    this.listado = this.listado.filter((val, i) => i != index);
    this.actual = null;
    this.displayDialog = false;
  }

  close() {
    this.actual = new Colocar('', '', '', [],0,'');
    this.displayDialog = false;
  }




  // FUNCIONES para el AUTOCOMPLETAR FABRICACION PEDIDO_DETALLE  PEDIDO ***************************************************************************

  filteredObjSinglePedido:any[];
  public fabricacion:any;

  filterSinglePedido(event) {
    let query = event.query;
    this._fabricarService.getAllFinalizado().subscribe(objs => {
      this.filteredObjSinglePedido = this.filterPedido(query, objs.fabricacions);
    });
  }

  filterPedido(query, objs:any[]):any[] {
    //in a real application, make a request to a remote url with the query and return filtered results, for demo we filter at client side
    let filtered:any[] = [];
    for (let i = 0; i < objs.length; i++) {
      let obj = objs[i];
      if (obj.pedido.pedido.descripcion.toLowerCase().indexOf(query.toLowerCase()) >= 0) {
        filtered.push(obj);
      }
    }
    return filtered;
  }
  //FIN AUTOCOMPLETAR PEDIDO *******************************************************************************************

  // FUNCIONES para el AUTOCOMPLETAR  RESPONSABLE **********************************************************************
  filteredObjSingleResponsable:any[];
  public responsable:any;

  filterSingleResponsable(event) {
    let query = event.query;
    this._personalService.getAll().subscribe(objs => {
      this.filteredObjSingleResponsable = this.filterResponsable(query, objs.personals);
    });
  }

  filterResponsable(query, objs:any[]):any[] {
    //in a real application, make a request to a remote url with the query and return filtered results, for demo we filter at client side
    let filtered:any[] = [];
    for (let i = 0; i < objs.length; i++) {
      let obj = objs[i];
      if (obj.apellido.toLowerCase().indexOf(query.toLowerCase()) >= 0) {
        filtered.push(obj);
      }
    }
    return filtered;
  }
  //FIN AUTOCOMPLETAR RESPONSABLE **************************************************************************************


  //FUNCIONES para ESTADOS**********************************************************************************************
  showDialogToAddEstado() {
    this.newObjEstado = true;
    this.estados = new EstadoObj('', '');
    this.displayDialogEstado = true;
  }

  saveEstado() {
    let listadoEstado = [...this.listadoEstado];
    if (this.newObjEstado)
      listadoEstado.push(this.estados);
    else
      listadoEstado[this.findSelectedIndexEstado()] = this.estados;

    this.listadoEstado = listadoEstado;
    this.estados = new EstadoObj('', '');
    this.displayDialogEstado = false;
  }

  deleteEstado() {
    let index = this.findSelectedIndexEstado();
    this.listadoEstado = this.listadoEstado.filter((val, i) => i != index);
    this.estados = null;
    this.displayDialogEstado = false;
  }

  onRowSelectEstado(event) {
    this.newObjEstado = false;
    var datePipe = new dateFormatPipe();
    event.data['fecha'] = datePipe.transform(event.data['fecha']);
    this.estados = this.cloneObjEstado(event.data);
    this.displayDialogEstado = true;


  }

  cloneObjEstado(obj:any):any {
    let actual = new EstadoObj('', '');
    for (let prop in obj) {
      actual[prop] = obj[prop];
    }
    return actual;
  }

  findSelectedIndexEstado():number {
    return this.listadoEstado.indexOf(this.selectedEstado);
  }
  //FIN FUNCIONES ESTADOS***********************************************************************************************


  onRowSelect(event) {
    this.newObj = false;
    this.actual = this.cloneObj(event.data);
    this.displayDialog = true;
    this.fabricacion = this.actual.pedido;
    this.responsable = this.actual.responsable;
    this.listadoEstado = this.actual.estado;
  }

  cloneObj(obj: Colocar): Colocar {
    let actual = new PrimeObj('', '', '', [],0,'');
    for (let prop in obj) {
      actual[prop] = obj[prop];
    }
    return actual;
  }

  findSelectedIndex(): number {
    return this.listado.indexOf(this.selected);
  }


  public filesToUpload: Array<File>;
  fileChangeEvent(fileInput: any) {
    this.filesToUpload = <Array<File>>fileInput.target.files;

  }

}

class PrimeObj implements Colocar {

  constructor(public pedido, public responsable, public estadoGeneral, public estado, public borrado, public _id) {
  }
}


class EstadoObj {
  constructor(public descripcion, public fecha){

  }
}







