import { Pipe, PipeTransform } from '@angular/core';
import * as moment from 'moment';

@Pipe({
  name: 'dateFormatPipe',
})

export class dateFormatPipe implements PipeTransform {
  transform(date: any, args?: any): any {
    let d = new Date(date);
    return moment(d).format('MM/DD/YYYY');

  }
}



