import { Component, DoCheck, OnInit } from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';
import {UserService} from '../../../services/user.service';
import {User} from "../../../models/user";
import {MessageService, SelectItem} from "primeng/api";
import {Validators, FormControl, FormGroup, FormBuilder} from "@angular/forms";


@Component({
  selector: 'admin-list',
  templateUrl: './list.component.html',
  styleUrls:['./list.component.css'],
  providers:[
    UserService,
    MessageService
  ]

})
export class ListComponent implements OnInit{
  title = 'Usuarios';
  numbers = new Array(10);
  frm: FormGroup;

    public identity;
    public token;


    public actual:User;
    public selected:User;
    public listado:any;
    public status:string;
    displayDialog:boolean;
    newObj:boolean;
    any:any;

    public roles : SelectItem[];
    selectedRoles: string[] = [];


    constructor(private _route:ActivatedRoute,
                private _router:Router,
                private _usuarioService:UserService,
                private fb: FormBuilder, private messageService: MessageService
    ) {
      this.actual = new User('','','','','','','',0);
      this.listado = [];
      this.getAll();
      this.identity = this._usuarioService.getIdentity();
      this.token = this._usuarioService.getToken();
      //this.actual = this.identity;

      this.roles = [
        {label: "ROLE_ADMIN", value: "ROLE_ADMIN"},
        {label: "ROLE_VENDEDOR", value: "ROLE_VENDEDOR"},
        {label: "ROLE_FABRICACION", value: "ROLE_FABRICACION"},
        {label: "ROLE_COLOCACION", value: "ROLE_COLOCACION"},
        {label: "ROLE_INSTALACION", value: "ROLE_INSTALACION"}
      ];
    }

    ngOnInit(){
      this.frm = this.fb.group({
        'rol': new FormControl('',Validators.compose([Validators.required])),
        'apellido': new FormControl('', Validators.compose([Validators.required, Validators.minLength(4)])),
        'nombre': new FormControl('', Validators.compose([Validators.required, Validators.minLength(4)])),
        'email': new FormControl('', Validators.compose([Validators.required, Validators.minLength(6), Validators.email])),
        'password': new FormControl('', Validators.compose([Validators.required, Validators.minLength(4)]))
      });
    }

    getAll() {
      this._usuarioService.getAll().subscribe(response => {
          this.listado = response.users;
          console.log(this.listado);
        },
        error=> {
          var errorMessage = <any>error;
          if (errorMessage != null) {
            this.status = 'error';
          }
        }
      );
    }

    showDialogToAdd() {
      this.newObj = true;
      this.actual = new User('','','','','','','',0);
      this.displayDialog = true;

    }

  deleteUpdate() {
    //funcion update
    this.actual.borrado = 1;
    this._usuarioService.updateUserPassword(this.token, this.actual).subscribe(
      response => {
        if (response) {
          this.status = "success";
          this.getAll();
          //listado[this.findSelectedIndex()] = this.actual;
          this.actual = new User('','','','','','','',0);
          this.messageService.add({severity:'success', summary:'Success', detail:'Información actualizada'});
        } else {
          console.log("OBJ _id: error" + JSON.stringify(response));
          this.status = "error";
        }
      },
      error => {
        console.log(<any>error);
      }
    );
    this.actual = new User('','','','','','','',0);
    this.displayDialog = false;
  }

    save() {
      let listado = [...this.listado];
      if (this.newObj) {
        // funcion save


        this._usuarioService.register(this.actual).subscribe(
          response => {
            if (response) {
              this.status = "success";
              this.actual = new User('','','','','','','',0);
              this.getAll();
              this.messageService.add({severity:'success', summary:'Success', detail:'Información ingresada'});
              //listado.push(this.actual);
            } else {
              this.status = "error";
            }
          },
          error => {
            console.log(<any>error);
          }
        );


      } else {
        //funcion update
        console.log("to save: " + JSON.stringify(this.actual));
        this._usuarioService.updateUserPassword(this.token, this.actual).subscribe(
          response => {
            if (response) {
              this.status = "success";
              this.getAll();
              //listado[this.findSelectedIndex()] = this.actual;
              this.actual = new User('','','','','','','',0);
              this.messageService.add({severity:'success', summary:'Success', detail:'Información actualizada'});
            } else {
              console.log("OBJ _id: error" + JSON.stringify(response));
              this.status = "error";
            }
          },
          error => {
            console.log(<any>error);
          }
        );
      }

      this.actual = new User('','','','','','','',0);
      this.displayDialog = false;


      //this.listado = listado;

    }

    delete() {
      let index = this.findSelectedIndex();
      this.listado = this.listado.filter((val, i) => i != index);
      this.actual = null;
      this.displayDialog = false;
    }

    close() {
      this.actual =new User('','','','','','','',0);
      this.displayDialog = false;
    }

    onRowSelect(event) {
      this.newObj = false;
      this.actual = this.cloneObj(event.data);
      this.actual.password="aaaa";
      this.displayDialog = true;
    }

    cloneObj(obj:User):User {
      let actual = new PrimeObj('', '', '', '', '', '', '',0);
      for (let prop in obj) {
        actual[prop] = obj[prop];
      }
      return actual;
    }

    findSelectedIndex():number {
      return this.listado.indexOf(this.selected);
    }

}


class PrimeObj implements User {

  constructor(public _id, public name, public surname, public email, public password, public role,
              public image, public borrado) {
  }
}



