import {Component, DoCheck, OnInit} from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';
import {UserService} from '../../../services/user.service';
import {SelectItem} from "primeng/api";
import {UploadService} from '../../../services/upload.service';
import {GLOBAL} from "../../../services/global";

import {Pedido} from "../../../models/pedido";
import {Fabricar} from "../../../models/fabricar";
import {Colocar} from "../../../models/colocar";
import {Instalar} from "../../../models/instalar";
import {Cliente} from "../../../models/cliente";
import {PedidoDetalle} from "../../../models/pedidoDetalle";

import {PedidoService} from "../../../services/pedido.service";
import {PedidoDetalleService} from "../../../services/pedidoDetalle.service";
import {FabricarService} from "../../../services/fabricar.service";
import {ColocarService} from "../../../services/colocar.service";
import {InstalarService} from "../../../services/instalar.service";
import {dateFormatPipe} from "../../../pipes/datePipe";
import {ClienteService} from "../../../services/cliente.service";


@Component({
  selector: 'usuario-edit',
  templateUrl: './seguimiento.component.html',
  providers: [

    UserService,
    UploadService,
    PedidoService,
    FabricarService,
    ColocarService,
    InstalarService,
    ClienteService,
    PedidoDetalleService
  ]
})
export class SeguimientoComponent {
  title = 'SEGUIMIENTO PEDIDOS';

  public identity;
  public token;
  public url: string;

  public buscarCI: string;

  public imagenFinal:any;

  public objPedido: PedidoDetalle;
  public objPedidoDetalle: PedidoDetalle;
  public objFabricar: Fabricar;
  public objColocar: Colocar;
  public objInstalar: Instalar;
  public objCliente: Cliente;

  listadoEstadoFabricacion: any[];
  listadoEstadoColocacion: any[];
  listadoEstadoInstalacion: any[];

  estadoFabricacion: any;
  estadoColocacion: any;
  estadoInstalacion: any;
  porcentajeFabricacion: number;
  porcentajeColocacion: number;
  porcentajeInstalacion: number;

  public selected: any;

  public listadoPedidos: any[];
  public listaPedidosDetalles: any[];


  public status: string;
  displayDialog: boolean;
  newObj: boolean;
  any: any;
  auxId: any;

  public roles: SelectItem[];


  constructor(private _route: ActivatedRoute,
              private _router: Router,
              private _usuarioService: UserService,
              private _uploadService: UploadService,
              private _pedidoService: PedidoService,
              private _fabricarService: FabricarService,
              private _colocarService: ColocarService,
              private _instalarService: InstalarService,
              private _clienteService: ClienteService,
              private _pedidoDetalleService: PedidoDetalleService
  ) {

    this.listadoPedidos = [];
    this.identity = this._usuarioService.getIdentity();
    this.token = this._usuarioService.getToken();
    this.url = GLOBAL.url;
    //this.actual = this.identity;

    this.objFabricar = null;
    this.objColocar = null;
    this.objInstalar = null;

  }

  ngOnInit() {

  }

  findByCI() {
    let listado: any[] = [];
    this._pedidoDetalleService.getAll().subscribe(response => {
        listado = response.pedidoDetalles;

        let filtered: any[] = [];
        for (let i = 0; i < listado.length; i++) {
          let obj = listado[i];
          if (obj.pedido.cliente.ci.indexOf(this.buscarCI) == 0) {
            filtered.push(obj);

          }
        }
        this.listadoPedidos = filtered;

      },
      error => {
        var errorMessage = <any>error;
        if (errorMessage != null) {
          this.status = 'error';
        }
      }
    );
  }

  onRowSelect(event) {
    var datePipe = new dateFormatPipe();
    event.data.pedido['fecha'] = datePipe.transform(event.data.pedido['fecha']);

    this.displayDialog = true;
    this.objPedido = this.cloneObjDetalle(event.data);

    this.fillEstadoFabricacion(event);



    //this.calcularBarrasPorcentajes();

    //console.log("objPedido: " + JSON.stringify(this.objPedido));
    //console.log("objCliente: " + JSON.stringify(this.objCliente));

  }

  fillEstadoFabricacion(event) {



    //this.objFabricar =JSON.stringify(event.data);


    this._fabricarService.getPedidoDetalle(event.data._id).subscribe(response => {
        this.objFabricar = response.fabricacion;
        console.log("objFABRICAR: " + this.objFabricar._id);
        this.fillEstadoColocacion();
        //let listado: any[] = response.fabricacions;

        /* let filtered: any[] = [];
         for (let i = 0; i < listado.length; i++) {
           let obj = listado[i];
           if (obj.pedido._id.indexOf(event.data._id) == 0) {
             filtered.push(obj);
           }
         }*/

        this.estadoFabricacion = null;
        if (this.objFabricar != null) {
          this.listadoEstadoFabricacion = this.objFabricar.estado;
          this.estadoFabricacion = this.objFabricar.estadoGeneral;
        } else {
          this.listadoEstadoFabricacion = [];
        }

        if (this.estadoFabricacion == 'Finalizado') {
          this.porcentajeFabricacion = 100;
        } else {

          this.porcentajeFabricacion = 10 * this.listadoEstadoFabricacion.length;
          if (this.porcentajeFabricacion > 100) {
            this.porcentajeFabricacion = 99;
          }
        }


      },
      error => {
        var errorMessage = <any>error;
        if (errorMessage != null) {
          this.status = 'error';
        }
      }
    );
  }

  fillEstadoColocacion() {
    this._colocarService.getFabricacion(this.objFabricar._id).subscribe(response => {
        this.objColocar  = response.colocacion;
        console.log("objCOLOCACION: " + this.objColocar._id);
        this.fillEstadoInstalacion();


        this.estadoColocacion = null;
        if (this.objColocar != null) {
          this.listadoEstadoColocacion = this.objColocar .estado;
          this.estadoColocacion = this.objColocar .estadoGeneral;
        } else {
          this.listadoEstadoColocacion = [];
        }

        if (this.estadoColocacion == 'Finalizado') {
          this.porcentajeColocacion = 100;
        } else {
          this.porcentajeColocacion = 10 * this.listadoEstadoColocacion.length;
          if (this.porcentajeColocacion > 100) {
            this.porcentajeColocacion = 99;
          }
        }


      },
      error => {
        var errorMessage = <any>error;
        if (errorMessage != null) {
          this.status = 'error';
        }
      }
    );
  }

  fillEstadoInstalacion() {
    this._instalarService.getColocacion(this.objColocar._id).subscribe(response => {
        this.objInstalar = response.instalacion;

        this.imagenFinal = this.objInstalar.imagen;
        console.log("objINSTALACION: " + this.imagenFinal);


        this.estadoInstalacion = null;
        if (this.objInstalar!= null) {
          this.listadoEstadoInstalacion = this.objInstalar.estado;
          this.estadoInstalacion = this.objInstalar.estadoGeneral;
        } else {
          this.listadoEstadoInstalacion = [];
        }

        if (this.estadoInstalacion == 'Finalizado') {
          this.porcentajeInstalacion = 100;
        } else {
          this.porcentajeInstalacion = 10 * this.listadoEstadoInstalacion.length;
          if (this.porcentajeInstalacion > 100) {
            this.porcentajeInstalacion = 99;
          }
        }
      },
      error => {
        var errorMessage = <any>error;
        if (errorMessage != null) {
          this.status = 'error';
        }
      }
    );
  }

  calcularBarrasPorcentajes() {
    if (this.estadoFabricacion == 'Finalizado') {
      this.porcentajeFabricacion = 100;
    } else {

      this.porcentajeFabricacion = 5 * this.listadoEstadoFabricacion.length;
    }

    if (this.estadoColocacion == 'Finalizado') {
      this.porcentajeColocacion = 100;
    } else {
      this.porcentajeColocacion = 5 * this.listadoEstadoColocacion.length;
    }

    if (this.estadoInstalacion == 'Finalizado') {
      this.porcentajeInstalacion = 100;
    } else {
      this.porcentajeInstalacion = 5 * this.listadoEstadoInstalacion.length;
    }
  }

  cloneObjPedido(obj: Pedido): Pedido {
    let actual = new PrimeObjPedido('', '', '', 0, 0, 0, 0, 0, 0, 0, '', new Date(), new Date(), '', 0);
    for (let prop in obj) {
      actual[prop] = obj[prop];
    }
    return actual;
  }

  cloneObjFabricar(obj: Fabricar): Fabricar {
    let actual = new PrimeObjFabricar('', '', '', [], [], 0,'');
    for (let prop in obj) {
      actual[prop] = obj[prop];
    }
    return actual;
  }

  cloneObjColocar(obj: Colocar): Colocar {
    let actual = new PrimeObjColocar('', '', '', [], 0,'');
    for (let prop in obj) {
      actual[prop] = obj[prop];
    }
    return actual;
  }

  cloneObjInstalar(obj: Instalar): Instalar {
    let actual = new PrimeObjInstalar('', '', '', '', [], 0, '','');
    for (let prop in obj) {
      actual[prop] = obj[prop];
    }
    return actual;
  }

  cloneObjCliente(obj: Cliente): Cliente {
    let actual = new PrimeObjCliente('', '', '', '', '', '', '', 0);
    for (let prop in obj) {
      actual[prop] = obj[prop];
    }
    return actual;

  }

  cloneObjDetalle(obj: PedidoDetalle): PedidoDetalle {
    let actual = new PrimeObjDetalle(0, 0, 0, 0, 0, 0, 0, '', '', '', '', 0);
    for (let prop in obj) {
      actual[prop] = obj[prop];
    }
    return actual;
  }

}

class PrimeObjPedido implements Pedido {

  constructor(public cliente, public modelo, public responsable, public ancho, public alto, public cantidad, public pvp, public descuento, public iva, public total, public descripcion, public fecha, public fecha_fin, public estado, public borrado) {
  }
}

class PrimeObjFabricar implements Fabricar {

  constructor(public pedido, public responsable, public estadoGeneral, public estado, public materiales, public borrado, public _id) {
  }
}

class PrimeObjColocar implements Colocar {

  constructor(public pedido, public responsable, public estadoGeneral, public estado, public borrado, public _id) {
  }
}

class PrimeObjInstalar implements Instalar {

  constructor(public pedido, public responsable, public estadoGeneral, public direccion, public estado, public borrado, public imagen, public _id) {
  }
}

class PrimeObjCliente implements Cliente {

  constructor(public ci, public apellido, public nombre, public ciudad, public direccion, public telefono, public email, public borrado) {
  }
}

class PrimeObjDetalle implements PedidoDetalle {

  constructor(public ancho, public alto, public cantidad, public pvp, public descuento, public iva, public total, public descripcion, public estado, public pedido, public modelo, public borrado) {
  }
}





