import {Component, DoCheck, OnInit} from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';
import {UserService} from '../../../services/user.service';
import {ModeloService} from '../../../services/modelo.service';
import {Modelo} from "../../../models/modelo";
import {SelectItem} from "primeng/api";
import {UploadService} from '../../../services/upload.service';
import {GLOBAL} from "../../../services/global";
import {CategoriaService} from "../../../services/categoria.service";


@Component({
  selector: 'vendedor-edit',
  templateUrl: './modelo.component.html',
  providers: [
    ModeloService,
    UserService,
    UploadService,
    CategoriaService
  ]
})
export class ModeloComponent {
  title = 'GESTION MODELOS';

  public identity;
  public token;
  public url: string;


  public actual: Modelo;
  public selected: Modelo;
  public listado: any;
  public status: string;
  displayDialog: boolean;
  newObj: boolean;
  any: any;
  auxId: any;

  public roles: SelectItem[];


  constructor(private _route: ActivatedRoute,
              private _router: Router,
              private _usuarioService: UserService,
              private _modeloService: ModeloService,
              private _uploadService: UploadService,
              private _categoriaService: CategoriaService
  ) {
    this.actual = new Modelo('', '', '',0, '',0);
    this.listado = [];
    this.getAll();
    this.identity = this._usuarioService.getIdentity();
    this.token = this._usuarioService.getToken();
    this.url = GLOBAL.url;
    //this.actual = this.identity;

  }

  ngOnInit() {

  }

  getAll() {
    this._modeloService.getAll().subscribe(response => {
        this.listado = response.modelos;
        console.log(this.listado);
      },
      error => {
        var errorMessage = <any>error;
        if (errorMessage != null) {
          this.status = 'error';
        }
      }
    );
  }

  showDialogToAdd() {
    this.newObj = true;
    this.actual = new Modelo('', '', '',0, '',0);
    this.displayDialog = true;
    this.categoria = null;

  }

  deleteUpdate() {
    //funcion update
    this.actual.borrado = 1;
    this._modeloService.update(this.token, this.actual).subscribe(
      response => {
        if (response.modelo) {
            this.status = "success";
            this.actual = new Modelo('', '', '',0, '',0);
            this.getAll();
        } else {
          console.log("OBJ _id: error" + JSON.stringify(response));
          this.status = "error";
        }
      },
      error => {
        console.log(<any>error);
      }
    );
    this.actual = new Modelo('', '', '',0, '',0);
    this.displayDialog = false;
  }

  save() {
    let listado = [...this.listado];
    this.actual.categoria = this.categoria._id;
    if (this.newObj) {
      // funcion save

     // console.log("before: " + this.newObj + "--" + JSON.stringify(this.actual));
      this._modeloService.save(this.token, this.actual).subscribe(
        response => {
          if (response) {

            console.log("before_imagen: " + response);
            if(this.filesToUpload.length>0){
              this._uploadService.makeFileRequest(this.url + 'upload-image-modelo/' + response.modelo._id, [], this.filesToUpload, this.token, 'imagen')
                .then((result: any) => {

                  this.status = "success";
                  this.actual = new Modelo('', '', '', 0,'',0);
                  this.getAll();
                  this.filesToUpload = new Array<File>();
                });
            }else{
              this.status = "success";
              this.actual = new Modelo('', '', '',0, '',0);
              this.getAll();
            }

            //listado.push(this.actual);
          } else {
            this.status = "error";
          }
        },
        error => {
          console.log("buuuuu");
          console.log(<any>error);
        }
      );


    } else {

      //funcion update

      this._modeloService.update(this.token, this.actual).subscribe(
        response => {
          if (response.modelo) {
            this.status = "success";
            if(this.filesToUpload) {
              this._uploadService.makeFileRequest(this.url + 'upload-image-modelo/' + response.modelo._id, [], this.filesToUpload, this.token, 'imagen')
                .then((result: any) => {
                  this.getAll();
                  this.actual = new Modelo('', '', '',0, '',0);
                  this.filesToUpload = null;
                });
            }else{
              this.status = "success";
              this.actual = new Modelo('', '', '',0, '',0);
              this.getAll();
            }

          } else {
            console.log("OBJ _id: error" + JSON.stringify(response));
            this.status = "error";
          }
        },
        error => {
          console.log(<any>error);
        }
      );
    }

    this.actual = new Modelo('', '', '',0, '',0);
    this.displayDialog = false;


    //this.listado = listado;

  }

  delete() {
    let index = this.findSelectedIndex();
    this.listado = this.listado.filter((val, i) => i != index);
    this.actual = null;
    this.displayDialog = false;
  }

  close() {
    this.actual = new Modelo('', '', '',0, '',0);
    this.displayDialog = false;
  }




  // FUNCIONES para el AUTOCOMPLETAR  CATEGORIA **************************************************************************
  objsModelo:any[];
  filteredObjSingleCategoria:any[];
  public categoria:any;

  filterSingleCategoria(event) {
    let query = event.query;
    this._categoriaService.getAll().subscribe(objs => {
      this.filteredObjSingleCategoria = this.filterCategoria(query, objs.categorias);
    });
  }

  filterCategoria(query, objs:any[]):any[] {
    //in a real application, make a request to a remote url with the query and return filtered results, for demo we filter at client side
    let filtered:any[] = [];
    for (let i = 0; i < objs.length; i++) {
      let obj = objs[i];
      if (obj.nombre.toLowerCase().indexOf(query.toLowerCase()) >= 0) {
        filtered.push(obj);
      }
    }
    return filtered;
  }
  //FIN AUTOCOMPLETAR CATEGORIA ******************************************************************************************

  onRowSelect(event) {
    this.newObj = false;
    this.actual = this.cloneObj(event.data);
    this.displayDialog = true;
    this.categoria = this.actual.categoria;
  }

  cloneObj(obj: Modelo): Modelo {
    let actual = new PrimeObj('', '', '', 0, '',0);
    for (let prop in obj) {
      actual[prop] = obj[prop];
    }
    return actual;
  }

  findSelectedIndex(): number {
    return this.listado.indexOf(this.selected);
  }


  public filesToUpload: Array<File>;
  fileChangeEvent(fileInput: any) {
    this.filesToUpload = <Array<File>>fileInput.target.files;

  }

}


class PrimeObj implements Modelo {

  constructor(public categoria, public nombre, public descripcion, public precio, public imagen, public borrado) {
  }
}



