import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {ColocadorGuard} from "../services/colocador.guard";

//components
import {MainComponent} from './components/main/main.component';
import {ColocarComponent} from './components/colocar/colocar.component';


//routes
const colocacionRoutes: Routes = [
  {
    path: 'colocacion-panel',
    component: MainComponent,
    canActivate: [ColocadorGuard],
    children:[
      { path: '', redirectTo:'colocar', pathMatch: 'full' },
      { path: 'colocar', component: ColocarComponent }
    ]
  }
];

@NgModule({
  imports:[
    RouterModule.forChild(colocacionRoutes)
  ],
  exports:[
    RouterModule
  ]
})

export class ColocacionRoutingModule{}



