//Imports modulos
import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {FormsModule,Validators,FormControl,FormGroup,FormBuilder} from '@angular/forms';
import {HttpModule} from '@angular/http';
import {VendedorRoutingModule} from './vendedor-routing.module';

//modulos ngPRIME
import {DataTableModule,SharedModule} from 'primeng/primeng';
import {ButtonModule} from 'primeng/primeng';
import {DialogModule} from 'primeng/primeng';
import {InputTextModule} from 'primeng/primeng';
import {PanelModule} from 'primeng/primeng';
import {DataListModule} from 'primeng/primeng';
import {CalendarModule} from 'primeng/primeng';
import {InputSwitchModule} from 'primeng/primeng';
import {GrowlModule} from 'primeng/primeng';
import {TabViewModule} from 'primeng/primeng';
import {PasswordModule} from 'primeng/password';
import {DropdownModule} from 'primeng/dropdown';
import { AutoCompleteModule } from 'primeng/primeng';
import {KeyFilterModule} from 'primeng/keyfilter';



//prime icons fa fa-
import { AngularFontAwesomeModule } from 'angular-font-awesome';

//components
import {MainComponent} from './components/main/main.component';
import {CategoriaComponent} from './components/categoria/categoria.component';
import {ModeloComponent} from './components/modelo/modelo.component';
import {ClienteComponent} from './components/cliente/cliente.component';
import {PedidoComponent} from './components/pedido/pedido.component';


//Guards
import {UserService} from "../services/user.service";
import {VendedorGuard} from "../services/vendedor.guard";
import {User} from "../models/user";


//Decorar ngModule para cargar los componentes y la configuracion de los modulos
@NgModule({
  imports:[
    CommonModule,
    FormsModule,




    HttpModule,
    VendedorRoutingModule,

    AngularFontAwesomeModule,


    DropdownModule,
    PasswordModule,
    DataTableModule,
    SharedModule,
    ButtonModule,
    DialogModule,
    InputTextModule,
    PanelModule,
    DataListModule,
    CalendarModule,
    InputSwitchModule,
    GrowlModule,
    TabViewModule,
    AutoCompleteModule,
    KeyFilterModule,




  ],
  declarations:[
    MainComponent,
    CategoriaComponent,
    ModeloComponent,
    ClienteComponent,
    PedidoComponent

  ],

  exports:[
    MainComponent,
    CategoriaComponent,
    ModeloComponent,
    ClienteComponent,
    PedidoComponent

  ],
  providers:[
    UserService,
    VendedorGuard
  ]
})

export class VendedorModule {}



