import { Injectable } from '@angular/core';
import {Http, Response, Headers} from '@angular/http';
import 'rxjs/add/operator/map';
import {Observable} from 'rxjs/Observable';
import {GLOBAL} from './global';

@Injectable()
export class InstalarService {
  public url: string;

  constructor(private _http: Http) {
    this.url = GLOBAL.url;
  }

  getAll(){
    return this._http.get(this.url+'instalacions')
      .map(res=>res.json());
  }

  save(token, obj_to_register){

    let params = JSON.stringify(obj_to_register);
    let headers = new Headers({
      'Content-Type':'application/json',
      'Authorization': token
    });
    return this._http.post(this.url+'instalacion', params, {headers: headers})
      .map(res=>res.json());
  }

  update(token,obj_to_update){

    let params = JSON.stringify(obj_to_update);
    let headers = new Headers({
      'Content-Type':'application/json',
      'Authorization': token
    });


    return this._http.put(this.url+'update-instalacion/'+obj_to_update._id, params, {headers: headers})
      .map(res=>res.json());
  }

  get(obj_to_search){
    let params = JSON.stringify(obj_to_search);
    let headers = new Headers({'Content-Type':'application/json'});
    return this._http.get(this.url+'instalacion/'+obj_to_search._id, {headers: headers})
      .map(res=>res.json());
  }

  getColocacion(id){

    let headers = new Headers({'Content-Type':'application/json'});
    return this._http.get(this.url+'instalacionColocacion/'+id, {headers: headers})
      .map(res=>res.json());
  }

  updateSubEstadoAdd(token,obj_to_update){

    let params = JSON.stringify(obj_to_update);
    let headers = new Headers({
      'Content-Type':'application/json',
      'Authorization': token
    });
    return this._http.put(this.url+'update-instalacionEstado/'+obj_to_update._id, params, {headers: headers})
      .map(res=>res.json());
  }

  updateSubEstadoUpdateById(token,obj_to_update){

    let params = JSON.stringify(obj_to_update);
    let headers = new Headers({
      'Content-Type':'application/json',
      'Authorization': token
    });
    return this._http.put(this.url+'update-instalacionEstadoUpdate/'+obj_to_update._id, params, {headers: headers})
      .map(res=>res.json());
  }


}



