//Importar modulos necesarios para crear modulos
import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {FormsModule} from '@angular/forms';

//importar componentes
import  {GuardarEmailComponent} from './components/guardar_email/guardar_email.component';
import  {MostrarEmailComponent} from './components/mostrar_email/mostrar_email.component';
import  {MainEmailComponent} from './components/main_email/main_email.component';

//Decorar ngModule para cargar los componentes y la configuracion de los modulos
@NgModule({
  imports:[CommonModule, FormsModule],
  declarations:[
    GuardarEmailComponent,
    MostrarEmailComponent,
    MainEmailComponent
  ],
  exports:[MainEmailComponent]
})

export class ModuloEmailModule{

}



