import {Component, DoCheck, OnInit} from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';
import {UserService} from '../../../services/user.service';
import {MateriaprimaService} from '../../../services/materiaprima.service';
import {Materiaprima} from "../../../models/materiaprima";
import {SelectItem} from "primeng/api";
import {UploadService} from '../../../services/upload.service';
import {CategoriaMaterialService} from '../../../services/categoriaMaterial.service';
import {GLOBAL} from "../../../services/global";


@Component({
  selector: 'admin-edit',
  templateUrl: './materiaprima.component.html',
  providers: [
    MateriaprimaService,
    UserService,
    UploadService,
    CategoriaMaterialService
  ]
})
export class MateriaprimaComponent {
  title = 'GESTION MATERIA PRIMA';

  public identity;
  public token;
  public url: string;


  public actual: Materiaprima;
  public selected: Materiaprima;
  public listado: any;
  public status: string;
  displayDialog: boolean;
  newObj: boolean;
  any: any;
  auxId: any;

  public unidades: SelectItem[];
  public rowGroupMetadata: any;



  constructor(private _route: ActivatedRoute,
              private _router: Router,
              private _usuarioService: UserService,
              private _materiaprimaService: MateriaprimaService,
              private _uploadService: UploadService,
              private _categoriaMaterialService: CategoriaMaterialService
  ) {
    this.actual = new Materiaprima('', '', '', 0, '',0,'');
    this.listado = [];
    this.getAll();

    this.identity = this._usuarioService.getIdentity();
    this.token = this._usuarioService.getToken();
    this.url = GLOBAL.url;
    //this.actual = this.identity;

    this.unidades = [
      {label: "Metro", value: "Metro"},
      {label: "Unidad", value: "Unidad"}
    ];

  }

  ngOnInit() {

  }

  updateRowGroupMetaData() {
    this.rowGroupMetadata = {};
    if (this.listado) {
      for (let i = 0; i < this.listado.length; i++) {
        let rowData = this.listado[i];
        let categoria = rowData.categoria.nombre;
        if (i == 0) {
          this.rowGroupMetadata[categoria] = { index: 0, size: 1 };
        }
        else {
          let previousRowData = this.listado[i - 1];
          let previousRowGroup = previousRowData.categoria.nombre;
          if (categoria === previousRowGroup)
            this.rowGroupMetadata[categoria].size++;
          else
            this.rowGroupMetadata[categoria] = { index: i, size: 1 };
        }
      }
    }
  }

  onSort() {
    this.updateRowGroupMetaData();
  }

  getAll() {
    this._materiaprimaService.getAll().subscribe(response => {
        this.listado = response.materiaPrimas;
        this.updateRowGroupMetaData();
        console.log(this.listado);
      },
      error => {
        var errorMessage = <any>error;
        if (errorMessage != null) {
          this.status = 'error';
        }
      }
    );
  }

  showDialogToAdd() {
    this.newObj = true;
    this.actual = new Materiaprima('', '', '', 0, '',0,'');
    this.displayDialog = true;

  }

  deleteUpdate() {
    //funcion update
    this.actual.borrado = 1;
    this._materiaprimaService.update(this.token, this.actual).subscribe(
      response => {
        if (response.materiaPrima) {
          this.status = "success";
            this.status = "success";
            this.actual = new Materiaprima('', '', '', 0, '',0,'');
            this.getAll();
        } else {
          console.log("OBJ _id: error" + JSON.stringify(response));
          this.status = "error";
        }
      },
      error => {
        console.log(<any>error);
      }
    );
    this.actual = new Materiaprima('', '', '', 0, '',0,'');
    this.displayDialog = false;
  }
  save() {
    let listado = [...this.listado];
    this.actual.categoria = this.categoria._id;
    if (this.newObj) {
      // funcion save
      if(this.actual.uMedida==''){
        this.actual.uMedida = 'Metro';
      }

      this._materiaprimaService.save(this.token, this.actual).subscribe(
        response => {
          if (response) {

            if(this.filesToUpload.length>0){
              this._uploadService.makeFileRequest(this.url + 'upload-image-materiaPrima/' + response.materiaPrima._id, [], this.filesToUpload, this.token, 'imagen')
                .then((result: any) => {

                  this.status = "success";
                  this.actual = new Materiaprima('', '', '', 0, '',0,'');
                  this.getAll();
                  this.filesToUpload = new Array<File>();
                });
            }else{
              this.status = "success";
              this.actual = new Materiaprima('', '', '', 0, '',0,'');
              this.getAll();
            }

            //listado.push(this.actual);
          } else {
            this.status = "error";
          }
        },
        error => {
          console.log(<any>error);
        }
      );


    } else {

      //funcion update
      this._materiaprimaService.update(this.token, this.actual).subscribe(
        response => {
          if (response.materiaPrima) {
            this.status = "success";
            if(this.filesToUpload) {
              this._uploadService.makeFileRequest(this.url + 'upload-image-materiaPrima/' + response.materiaPrima._id, [], this.filesToUpload, this.token, 'imagen')
                .then((result: any) => {
                  this.getAll();
                  this.actual = new Materiaprima('', '', '', 0, '',0,'');
                  this.filesToUpload = null;
                });
            }else{
              this.status = "success";
              this.actual = new Materiaprima('', '', '', 0, '',0,'');
              this.getAll();
            }

          } else {
            console.log("OBJ _id: error" + JSON.stringify(response));
            this.status = "error";
          }
        },
        error => {
          console.log(<any>error);
        }
      );
    }

    this.actual = new Materiaprima('', '', '', 0, '',0,'');
    this.displayDialog = false;


    //this.listado = listado;

  }

  delete() {
    let index = this.findSelectedIndex();
    this.listado = this.listado.filter((val, i) => i != index);
    this.actual = null;
    this.displayDialog = false;
  }

  close() {
    this.actual = new Materiaprima('', '', '', 0, '',0,'');
    this.displayDialog = false;
  }


// FUNCIONES para el AUTOCOMPLETAR  CATEGORIA **************************************************************************
  objsModelo:any[];
  filteredObjSingleCategoria:any[];
  public categoria:any;

  filterSingleCategoria(event) {
    let query = event.query;
    this._categoriaMaterialService.getAll().subscribe(objs => {
      this.filteredObjSingleCategoria = this.filterCategoria(query, objs.categorias);
    });
  }

  filterCategoria(query, objs:any[]):any[] {
    //in a real application, make a request to a remote url with the query and return filtered results, for demo we filter at client side
    let filtered:any[] = [];
    for (let i = 0; i < objs.length; i++) {
      let obj = objs[i];
      if (obj.nombre.toLowerCase().indexOf(query.toLowerCase()) >= 0) {
        filtered.push(obj);
      }
    }
    return filtered;
  }
  //FIN AUTOCOMPLETAR CATEGORIA ******************************************************************************************


  onRowSelect(event) {
    this.newObj = false;
    this.actual = this.cloneObj(event.data);
    this.displayDialog = true;
    this.categoria = this.actual.categoria;
  }

  cloneObj(obj: Materiaprima): Materiaprima {
    let actual = new PrimeObj('', '', '', 0, '',0,'');
    for (let prop in obj) {
      actual[prop] = obj[prop];
    }
    return actual;
  }

  findSelectedIndex(): number {
    return this.listado.indexOf(this.selected);
  }


  public filesToUpload: Array<File>;
  fileChangeEvent(fileInput: any) {
    this.filesToUpload = <Array<File>>fileInput.target.files;

  }

}


class PrimeObj implements Materiaprima {

  constructor(public descripcion, public uMedida, public color, public stock, public imagen, public borrado, public categoria) {
  }
}



