import { Component, DoCheck, OnInit } from '@angular/core';

@Component({
  selector: 'mostrar_email',
  template: `


    <div *ngIf="emailContacto">
    <h4>{{title}}</h4>
      <strong>Email de Contato:</strong> {{emailContacto}}
      <button (click)="borrarEmail()">Eliminar mailContacto</button>
    </div>

  `
})
export class MostrarEmailComponent  implements DoCheck, OnInit{
  title = 'Mostrar email';
  emailContacto :string;

  ngOnInit(){
    this.emailContacto = localStorage.getItem('emailContacto')

  }

  ngDoCheck(){
    this.emailContacto = localStorage.getItem('emailContacto')

  }

  borrarEmail(){
    localStorage.removeItem('emailContacto');
    localStorage.clear();
    this.emailContacto = null;
  }
}



