import { Injectable } from '@angular/core';
import {Http, Response, Headers} from '@angular/http';
import 'rxjs/add/operator/map';
import {Observable} from 'rxjs/Observable';
import {GLOBAL} from './global';

@Injectable()
export class IvaService {
  public url: string;

  constructor(private _http: Http) {
    this.url = GLOBAL.url;
  }

  getAll(){
    return this._http.get(this.url+'ivas')
      .map(res=>res.json());
  }

  save(token, obj_to_register){

    let params = JSON.stringify(obj_to_register);
    let headers = new Headers({
      'Content-Type':'application/json',
      'Authorization': token
    });
    return this._http.post(this.url+'iva', params, {headers: headers})
      .map(res=>res.json());
  }

  update(token,obj_to_update){

    let params = JSON.stringify(obj_to_update);
    let headers = new Headers({
      'Content-Type':'application/json',
      'Authorization': token
    });


    return this._http.put(this.url+'update-iva/'+obj_to_update._id, params, {headers: headers})
      .map(res=>res.json());
  }

  get(obj_to_search){
    let params = JSON.stringify(obj_to_search);
    let headers = new Headers({'Content-Type':'application/json'});
    return this._http.get(this.url+'iva/'+obj_to_search._id, {headers: headers})
      .map(res=>res.json());
  }
}



