import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';


//components

import {PersonalComponent} from './components/personal/personal.component';
import {ListComponent} from './components/list/list.component';
import {MateriaprimaComponent} from './components/materiaprima/materiaprima.component';
import {MainComponent} from './components/main/main.component';
import {CategoriaComponent} from './components/categoria/categoria.component';
import {AdminGuard} from "../services/admin.guard";
import {IvaComponent} from "./components/iva/iva.component";


//routes
const adminRoutes: Routes = [
  {
    path: 'admin-panel',
    component: MainComponent,
    canActivate: [AdminGuard],
    children:[
      { path: '', redirectTo:'listado', pathMatch: 'full' },
      { path: 'listado', component: ListComponent },
      { path: 'personal', component: PersonalComponent },
      { path: 'materiaPrima', component: MateriaprimaComponent },
      { path: 'categoria', component: CategoriaComponent },
      { path: 'iva', component: IvaComponent }
    ]
  }
];

@NgModule({
  imports:[
    RouterModule.forChild(adminRoutes)
  ],
  exports:[
    RouterModule
  ]
})

export class AdminRoutingModule{}



