import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {InstaladorGuard} from "../services/instalador.guard";

//components
import {MainComponent} from './components/main/main.component';
import {InstalarComponent} from './components/instalar/instalar.component';


//routes
const instalacionRoutes: Routes = [
  {
    path: 'instalacion-panel',
    component: MainComponent,
    canActivate: [InstaladorGuard],
    children:[
      { path: '', redirectTo:'instalar', pathMatch: 'full' },
      { path: 'instalar', component: InstalarComponent }
    ]
  }
];

@NgModule({
  imports:[
    RouterModule.forChild(instalacionRoutes)
  ],
  exports:[
    RouterModule
  ]
})

export class InstalacionRoutingModule{}



