//Imports modulos
import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {FormsModule,Validators,FormControl,FormGroup,FormBuilder} from '@angular/forms';
import {HttpModule} from '@angular/http';
import {UsuarioRoutingModule} from './usuario-routing.module';

//modulos ngPRIME
import {DataTableModule,SharedModule} from 'primeng/primeng';
import {ButtonModule} from 'primeng/primeng';
import {DialogModule} from 'primeng/primeng';
import {InputTextModule} from 'primeng/primeng';
import {PanelModule} from 'primeng/primeng';
import {DataListModule} from 'primeng/primeng';
import {CalendarModule} from 'primeng/primeng';
import {InputSwitchModule} from 'primeng/primeng';
import {GrowlModule} from 'primeng/primeng';
import {TabViewModule} from 'primeng/primeng';
import {PasswordModule} from 'primeng/password';
import {DropdownModule} from 'primeng/dropdown';
import { AutoCompleteModule } from 'primeng/primeng';
import {KeyFilterModule} from 'primeng/keyfilter';
import {ProgressBarModule} from 'primeng/progressbar';



//prime icons fa fa-
import { AngularFontAwesomeModule } from 'angular-font-awesome';

//components
import {MainComponent} from './components/main/main.component';
import {SeguimientoComponent} from './components/seguimiento/seguimiento.component';

//Guards
import {UserService} from "../services/user.service";
import {VendedorGuard} from "../services/vendedor.guard";
import {User} from "../models/user";


//Decorar ngModule para cargar los componentes y la configuracion de los modulos
@NgModule({
  imports:[
    CommonModule,
    FormsModule,




    HttpModule,
    UsuarioRoutingModule,

    AngularFontAwesomeModule,


    DropdownModule,
    PasswordModule,
    DataTableModule,
    SharedModule,
    ButtonModule,
    DialogModule,
    InputTextModule,
    PanelModule,
    DataListModule,
    CalendarModule,
    InputSwitchModule,
    GrowlModule,
    TabViewModule,
    AutoCompleteModule,
    KeyFilterModule,
    ProgressBarModule




  ],
  declarations:[
    MainComponent,
    SeguimientoComponent

  ],

  exports:[
    MainComponent,
    SeguimientoComponent

  ],
  providers:[
    UserService,
    VendedorGuard
  ]
})

export class UsuarioModule {}



