import {Component, DoCheck, OnInit} from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';
import {UserService} from '../../../services/user.service';
import {PersonalService} from '../../../services/personal.service';
import {Personal} from "../../../models/personal";
import {MessageService, SelectItem} from "primeng/api";
import {UploadService} from '../../../services/upload.service';
import {GLOBAL} from "../../../services/global";
import {Validators, FormControl, FormGroup, FormBuilder} from "@angular/forms";



@Component({
  selector: 'admin-edit',
  templateUrl: './personal.component.html',
  providers: [
    PersonalService,
    UserService,
    UploadService,
    MessageService
  ]
})
export class PersonalComponent {
  title = 'GESTION PERSONAL';
  frm: FormGroup;


  public identity;
  public token;
  public url: string;


  public actual: Personal;
  public selected: Personal;
  public listado: any;
  public status: string;
  displayDialog: boolean;
  newObj: boolean;
  any: any;
  auxId: any;

  public roles: SelectItem[];


  constructor(private _route: ActivatedRoute,
              private _router: Router,
              private _usuarioService: UserService,
              private _personalService: PersonalService,
              private _uploadService: UploadService,
              private fb: FormBuilder, private messageService: MessageService

  ) {
    this.actual = new Personal('', '', '', '', '', '', '',0);
    this.listado = [];
    this.getAll();
    this.identity = this._usuarioService.getIdentity();
    this.token = this._usuarioService.getToken();
    this.url = GLOBAL.url;
    //this.actual = this.identity;

  }

  ngOnInit() {
    this.frm = this.fb.group({
      'ci': new FormControl('',Validators.compose([Validators.required, Validators.minLength(10)])),
      'apellido': new FormControl('', Validators.compose([Validators.required, Validators.minLength(6)])),
      'nombre': new FormControl('', Validators.compose([Validators.required, Validators.minLength(6)])),
      'email': new FormControl('', Validators.compose([Validators.required, Validators.minLength(6), Validators.email])),
      'fono': new FormControl('', Validators.compose([Validators.required, Validators.minLength(9)]))
    });
  }

  getAll() {
    this._personalService.getAll().subscribe(response => {
        this.listado = response.personals;
        console.log(this.listado);
      },
      error => {
        var errorMessage = <any>error;
        if (errorMessage != null) {
          this.status = 'error';
        }
      }
    );
  }

  showDialogToAdd() {
    this.newObj = true;
    this.actual = new Personal('', '', '', '', '', '', '',0);
    this.displayDialog = true;

  }

  deleteUpdate() {
    //funcion update
    this.actual.borrado = 1;
    this._personalService.update(this.token, this.actual).subscribe(
      response => {
        if (response.personal) {
            this.status = "success";
            this.actual = new Personal('', '', '', '', '', '', '',0);
            this.getAll();
            this.messageService.add({severity:'success', summary:'Success', detail:'Información actualizada'});


        } else {
          console.log("OBJ _id: error" + JSON.stringify(response));
          this.status = "error";
        }
      },
      error => {
        console.log(<any>error);
      }
    );
    this.actual = new Personal('', '', '', '', '', '', '',0);
    this.displayDialog = false;
  }

  save() {
    let listado = [...this.listado];
    if (this.newObj) {
      // funcion save


      this._personalService.save(this.token, this.actual).subscribe(
        response => {
          if (response) {

            if(this.filesToUpload.length>0){
              this._uploadService.makeFileRequest(this.url + 'upload-image-personal/' + response.personal._id, [], this.filesToUpload, this.token, 'imagen')
                .then((result: any) => {

                  this.status = "success";
                  this.actual = new Personal('', '', '', '', '', '', '',0);
                  this.getAll();
                  this.filesToUpload = new Array<File>();
                });
            }else{
              this.status = "success";
              this.actual = new Personal('', '', '', '', '', '', '',0);
              this.getAll();
              this.messageService.add({severity:'success', summary:'Success', detail:'Información ingresada'});
            }

            //listado.push(this.actual);
          } else {
            this.status = "error";
          }
        },
        error => {
          console.log(<any>error);
        }
      );


    } else {

      //funcion update
      this._personalService.update(this.token, this.actual).subscribe(
        response => {
          if (response.personal) {
            this.status = "success";
            if(this.filesToUpload) {
              this._uploadService.makeFileRequest(this.url + 'upload-image-personal/' + response.personal._id, [], this.filesToUpload, this.token, 'imagen')
                .then((result: any) => {
                  this.getAll();
                  this.actual = new Personal('', '', '', '', '', '', '',0);
                  this.filesToUpload = null;
                });
            }else{
              this.status = "success";
              this.actual = new Personal('', '', '', '', '', '', '',0);
              this.getAll();
              this.messageService.add({severity:'success', summary:'Success', detail:'Información actualizada'});
            }

          } else {
            console.log("OBJ _id: error" + JSON.stringify(response));
            this.status = "error";
          }
        },
        error => {
          console.log(<any>error);
        }
      );
    }

    this.actual = new Personal('', '', '', '', '', '', '',0);
    this.displayDialog = false;


    //this.listado = listado;

  }

  delete() {
    let index = this.findSelectedIndex();
    this.listado = this.listado.filter((val, i) => i != index);
    this.actual = null;
    this.displayDialog = false;
  }

  close() {
    this.actual = new Personal('', '', '', '', '', '', '',0);
    this.displayDialog = false;
  }

  onRowSelect(event) {
    this.newObj = false;
    this.actual = this.cloneObj(event.data);
    this.displayDialog = true;
  }

  cloneObj(obj: Personal): Personal {
    let actual = new PrimeObj('', '', '', '', '', '', '',0);
    for (let prop in obj) {
      actual[prop] = obj[prop];
    }
    return actual;
  }

  findSelectedIndex(): number {
    return this.listado.indexOf(this.selected);
  }


  public filesToUpload: Array<File>;
  fileChangeEvent(fileInput: any) {
    this.filesToUpload = <Array<File>>fileInput.target.files;

  }

}


class PrimeObj implements Personal {

  constructor(public ci, public apellido, public nombre, public direccion, public telefono, public email, public imagen, public borrado) {
  }
}



