import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {FabricadorGuard} from "../services/fabricador.guard";

//components
import {MainComponent} from './components/main/main.component';
import {FabricarComponent} from './components/fabricar/fabricar.component';


//routes
const fabricacionRoutes: Routes = [
  {
    path: 'fabricacion-panel',
    component: MainComponent,
    canActivate: [FabricadorGuard],
    children:[
      { path: '', redirectTo:'fabricar', pathMatch: 'full' },
      { path: 'fabricar', component: FabricarComponent }
    ]
  }
];

@NgModule({
  imports:[
    RouterModule.forChild(fabricacionRoutes)
  ],
  exports:[
    RouterModule
  ]
})

export class FabricacionRoutingModule{}



