import {Component, Input, Output, EventEmitter, OnChanges, SimpleChanges, OnInit, OnDestroy}from  '@angular/core';

@Component({
  selector: 'parques',
  templateUrl:'./parques.component.html',

})

export class ParquesComponent implements OnChanges, OnInit, OnDestroy{
  @Input() nombre : string;
  @Input('metrosCuadrados') metros : number;
  public vegetacion: string;
  public abierto : boolean;

  @Output() pasameLosDatos = new EventEmitter();

  constructor(){
    this.nombre = 'Parque natural reserva';
    this.metros = 450;
    this.vegetacion = 'Alta';
    this.abierto = false;
  }

  ngOnChanges(changes : SimpleChanges){
    //console.log(changes);
    console.log("Existen cambios en las propiedades");
  }

  ngOnInit(){
    console.log("Metodo OnInit lanzado");
  }

  ngOnDestroy(){
    console.log("Metodo OnDestroy ON")
  }


  emitirEvento(){
    this.pasameLosDatos.emit({
      'nombre': this.nombre,
      'metros': this.metros,
      'vegetacion': this.vegetacion,
      'abierto': this.abierto
    });
  }
}



